<template>
  <div class="white-board-box" ref="wbBox">
    <div class="whiteboard-header">
      <span class="txt">
        {{isLock && !isWhiteBoarOrganiser ? $t('last.looking') : $t('last.noting')}}
      </span>
      <i class="wb-close el-icon-close" @click="isWhiteBoarOrganiser ? confirmLeavep = true :  exitBoard()"></i>
    </div>
    <canvas
      ref="canvas"
      id="canvas"
      width="1280"
      height="720"
      style="background-color: #ffffff;width:100vw;height:calc(100vh - 40px);"
      @click.stop="closeAllBox"
    ></canvas>
    <div ref="controller" :class="{controller:true,disabled:(isLock && !isWhiteBoarOrganiser) || isReconnect}" v-drag>
      <ul>
        <li
          v-for="item in controllerList"
          :key="item.id"
          v-if="(item.className!=='lock' && item.className!=='delete') ||
          ((item.className === 'lock' || item.className ==='delete') && isWhiteBoarOrganiser && !pTop && !receiveId)"
          @click="handleClickController(item)"
          :class="item.isActive"
        >
          <i :class="[item.className]"></i>
          <!--<span>{{item.name}}</span>-->
        </li>
      </ul>
      <!-- 画笔弹窗 -->
      <div v-if="BruchBoxShow.isShow" class="bruch-box">
        <ul>
          <li
            v-for="(it,index) in BruchBoxShow.list"
            :key="it.name"
            @click="handleClickBruch(it,index)"
            :class="it.isActive"
          >
            <i :class="[it.className]"></i>
            <span>{{it.name}}</span>
          </li>
        </ul>
      </div>
      <!-- 颜色弹窗 -->
      <div v-if="ColorBoxShow.isShow" class="color-box">
        <ul>
          <li
            v-for="(item,index) in ColorBoxShow.list"
            :key="item.className"
            @click="handleClickColor(item,index)"
          >
            <i :class="[item.className,item.isActive]"></i>
          </li>
        </ul>
      </div>
      <!-- 格式弹窗 -->
      <div v-if="LayoutBoxShow.isShow" class="layout-box">
        <div class="colorBox">
         <div
           @click="handleClickColor(item,index)"
           v-for="(item,index) in ColorBoxShow.list"
           :key="item+index"
           :class="[item.className,item.isActive]"
         >
           <i :class="item.className"></i>
         </div>
        </div>
        <div class="second">
          <h6>{{$t('last.penSize')}}</h6>
          <p>
            <i
              @click="handleClickFontWeight(item,index)"
              v-for="(item,index) in LayoutBoxShow.fontWeight"
              :key="item+index"
              :class="[item.className,item.isActive]"
            ></i>
          </p>
        </div>
      </div>
      <!-- 白板文档 -->
      <ul v-if="DocBoxShow.isShow" class="more-box">
        <li v-for="item in docArr" :class="docId === item.wbssDocId ? 'active' : ''" @click="shareDoc(item.wbssDocId)">
          <!--白板-->
          <div v-if="item.wbssDocId == 0"></div>
          <!--图片-->
          <img :src="fileObj[item.wbssDocId].path" v-else-if="fileObj[item.wbssDocId].fileType === 'img'" alt="">
          <!--文件-->
          <img :src="fileObj[item.wbssDocId] && fileObj[item.wbssDocId].fileType ? require('../../assets/img/whiteboard/FileTypeS_'+ fileObj[item.wbssDocId].fileType+'.png') : ''" v-else alt="">
          <span>{{item.wbssDocId == 0 ? $t('last.wb') : $t('last.doc') + item.wbssDocId}}</span>
        </li>
        <li class="add-doc" @click="addDoc" v-preventReClick="2000" style="pointer-events:auto;">
          <div></div>
          <span>{{$t('last.newWbFile')}}</span>
          <input type="file" ref="fileInput" v-show="false" @change="uploadDoc" :accept="fileAccept">
        </li>
      </ul>
    </div>
    <!-- 画板右下角 -->
    <div class="fonter">
      <!-- <i class="add" @click="addPagation"></i> -->
      <i class="left" v-if="isWhiteBoarOrganiser" @click="togglePrevPagation" v-show="!isLock || (isLock && isWhiteBoarOrganiser)"></i>
      <div class="text">
        <span>{{current}}</span>
        /
        <span>{{totalPage}}</span>
      </div>
      <i class="right" v-if="isWhiteBoarOrganiser" @click="toggleNextPagation" v-show="!isLock || (isLock && isWhiteBoarOrganiser)"></i>
      <!-- <i class="delete" @click="delPagation"></i> -->
    </div>
    <!-- 白板文本框 -->
    <el-input
      ref="WhiteBoardInputRef"
      class="whiteBoardInput"
      v-show="isWhiteBoardInput"
      v-model="whiteBoardInput"
      @keyup.enter.native="handlewhiteBoardInputBlur"
      @blur="handlewhiteBoardInputBlur"
      :placeholder="$t('last.chatTip')"
      autofocus
      v-drag
    ></el-input>
    <div class="white-board-zoom" v-show="(isLock && !isWhiteBoarOrganiser) || isReconnect"></div>

    <!--退出弹框-->
    <el-dialog
      :title="$t('modelMessage.prompt')"
      :visible.sync="confirmLeavep"
      width="450px"
      :modal="false"
      :close-on-click-modal="false"
      custom-class="confirmLeavepDialog"
      center
    >
      <div style="width:210px;margin:0 auto;line-height:25px;">
        <p style="font-size:18px; margin-bottom:20px;">{{$t('last.closeWbTip')}}</p>
        <el-checkbox v-if="isWhiteBoarOrganiser" v-model="isDelChecked">{{$t('last.deleteWb')}}</el-checkbox>
      </div>
      <span slot="footer" class="dialog-footer clearfix">
                <el-button type="default" @click="confirmLeavep=false">{{$t('btn.cancel')}}</el-button>
                <el-button type="success" style="margin-left:50px;" @click="exitBoard">{{$t('btn.determine')}}</el-button>
            </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapGetters } from "vuex";
export default {
  props: [
    "roomId",
    "userId",
    "isWhiteBoarOrganiser",
    'pTop',
    'receiveId',
    'wbInfo'
  ],
  computed: {
    ...mapState(["LoginServe"]),
    ...mapGetters(['meetInfo']),
  },
  watch: {
    meetInfo(val) {
      console.log('===getters.meetInfo===',val);
      if (val.state & 0x0008) {
        this.isLock = true;
        this.controllerList.forEach(item => {
          if(item.className == 'lock') {
            item.isActive = 'active';
          }
        });
      } else {
        this.isLock = false;
        this.controllerList.forEach(item => {
          if(item.className == 'lock') {
            item.isActive = '';
          }
        });
      }
    }
  },
  data() {
    return {
      confirmLeavep: false,
      isDelChecked: false,
      //控制条
      controllerList: [
        // { id: 1, name: "切换文档", className: "exchange", isActive: "", children: "DocBoxShow"},
        { id: 2, name: this.$t('last.brush'), className: "brush", children: "BruchBoxShow", isActive: "active" },
        { id: 3, name: this.$t('last.laser'), className: "laser", isActive: "" },
        { id: 4, name: this.$t('last.eraser'), className: "eraser", isActive: "" },
        { id: 5, name: this.$t('last.layout'), className: "layout", children: "LayoutBoxShow", isActive: "" },
        { id: 6, name: this.$t('last.reset'), className: "reset", isActive: "" },
        { id: 7, name: this.$t('last.redo'), className: "redo", isActive: "" },
        { id: 8, name: this.$t('last.delete'), className: "delete", isActive: "" },
        { id: 9, name: this.$t('last.lock'), className: "lock", isActive: "" },
        { id: 10, name: this.$t('last.save'), className: "save", isActive: "" },
        // { id: 11, name: "切换查看", className: "docShare", isActive: "" },
      ],
      //画笔控制条
      BruchBoxShow: {
        isShow: false,
        list: [
          { name: this.$t('last.beeline'), className: "beeline", isActive: "" },
          { name: this.$t('last.curve'), className: "curve", isActive: "active" },
          { name: this.$t('last.quadrangle'), className: "quadrangle", isActive: "" },
          { name: this.$t('last.trigon'), className: "trigon", isActive: "" },
          { name: this.$t('last.circle'), className: "circle", isActive: "" }
        ]
      },
      //颜色控制条
      ColorBoxShow: {
        isShow: false,
        list: [
          { className: "color_red", isActive: "", rgb: [255,51,58]},
          { className: "color_orange", isActive: "", rgb: [255, 95, 4] },
          { className: "color_yellow", isActive: "", rgb: [255, 220, 0] },
          { className: "color_green", isActive: "", rgb: [32, 201, 138] },
          { className: "color_blue", isActive: "", rgb: [20, 107, 230] },
          { className: "color_purple", isActive: "", rgb: [162, 64, 226] },
          { className: "color_gray", isActive: "", rgb: [231, 240, 249] },
          { className: "color_black", isActive: "", rgb: [70, 70, 70] }
        ]
      },
      //格式控制条
      LayoutBoxShow: {
        isShow: false,
        fontSize: [
          { className: "font-20", text: "20", isActive: "" },
          { className: "font-24", text: "24", isActive: "" },
          { className: "font-28", text: "28", isActive: "" },
          { className: "font-32", text: "32", isActive: "" },
          { className: "font-48", text: "48", isActive: "" }
        ],
        fontWeight: [
          { className: "font-20", isActive: "active", value: 3 },
          { className: "font-24", isActive: "", value: 6 },
          { className: "font-28", isActive: "", value: 8 },
        ]
      },
      // 更多面板
      MoreBoxShow: {
        isShow: false,
        value: false
      },
      // 文本框
      isWhiteBoardInput: false,
      // 文本内容
      whiteBoardInput: "",
      // 文本字体大小
      whiteBoardTextFontSize: 25,
      isLock: false,
      DocBoxShow: {
        isShow: false
      },
      docList: [],
      current: 1,
      totalPage: 10,
      docId: 0,
      docArr: [],
      wbRoomId: '',
      fileObj : {},
      fileType: {
        img: ["png", "jpg", "jpeg"],
        pdf: ['pdf'],
        ppt: ["ppt", "pps", "pot", "ppa", 'pptx'],
        word: ["doc", "dot", "xml", 'docx'],
        xls : ["xls", "xlt", "xla", 'xlsx']
      },
      fileAccept: '.png,.jpg,.jpeg,.pdf,.ppt,.pps,.pptx,.pot,.ppa,.doc,.docx,.dot,.xls,.xlt,.xlsx',
      isReconnect: false
    };
  },
  methods: {
    exitBoard() {
      if (this.pTop) {
        if (this.isDelChecked && this.isWhiteBoarOrganiser) {
          this.$bus.emit('stopWhiteBoardSharing');
        } else {
          this.$bus.emit('leaveWbRoom');
        }
      } else {
        if (this.isDelChecked && this.isWhiteBoarOrganiser) {
          this.$parent.closeWhiteBoardShare()
        } else {
          this.$parent.leaveWbRoom();
        }
      }
      this.confirmLeavep = false;
    },
    init() {
      console.log(`this.LoginServe`);
      console.log(this.LoginServe);
      let self = this;
      this.fileObj = {};
      let wbss_user_id = this.userId;
      let canvas = document.getElementById("canvas");
      let ret = YTX_WBSS_API.init_WBSS_SDK(
        wbss_user_id,
        "123456",
        this.LoginServe.AppId,
        // "https://rstest.yomeeting.com",
        // "https://rs52.yomeeting.com",
        // "https://192.168.180.190",
        // `https://${this.LoginServe.WbssAddrs[0]}`,
        this.LoginServe.WbssAddrs[0],
        // 'https://192.168.27.227:9001/rest/auth',
        canvas
      );
      this.isReconnect = false;
      console.log(ret);
      let state = this.meetInfo.state;
      if (state & 0x0008) {
        this.isLock = true;
      } else {
        this.isLock = false;
      }
      setTimeout(() => {
        this.current = this.wbInfo.current;
        this.docArr = this.wbInfo.docArr;
        this.docId = this.wbInfo.docId;
        this.totalPage = this.wbInfo.totalPage;
        this.wbRoomId = this.wbInfo.wbRoomId;
        this.shareDoc(this.docId, () => {
          this.gotoPage(2, this.current);
        });
        console.log('===docArr==',this.wbInfo);
      }, 700)

      // 设置白板区域，可以默认不设置
      YTX_WBSS_API.setWhiteBoardScale(0); // <=0 全屏分辨率比例

      // 注册监听器
      YTX_WBSS_API.onUploadDocProcessListener(function(resp) {
        console.log('===onUploadDocProcessListener===',resp);
      });
      YTX_WBSS_API.onDocConvertListener(resp => {
        console.log('===onDocConvertListener===',resp);
        resp.wbssRoomInfoMap.wbssDocInfoDataArray.forEach(item => {
          if (!this.fileObj[item.wbssDocId]) this.fileObj[item.wbssDocId] = {}
        })
        self.$set(self, 'docArr', resp.wbssRoomInfoMap.wbssDocInfoDataArray)
      });
      YTX_WBSS_API.onGotoPageListener(resp => {
        this.current = resp.pageId;
      });
      YTX_WBSS_API.onShareDocListener(function(resp) {});
      YTX_WBSS_API.onFilePathListener(res => {
        console.log('===onFilePathListener===',res)
        let ext = res.fileName.split('.');
        ext = ext[ext.length-1];
        res.ext = ext;
        for (let val in this.fileType) {
          if (this.fileType[val].includes(ext)) {
            res.fileType = val;
          }
        }
        this.$set(this.fileObj, res.docId, res);
        this.$forceUpdate();
        console.log('===fileObj===',this.fileObj);
      })
      YTX_WBSS_API.onConnectStateChangeListener(state => {
        if (state == 1) {
        //  网络掉线
          this.$message.warning('网络掉线')
          this.isReconnect = true;
        } else if (state == 2) {
        //  重连中
          this.$message.warning('重连中')
          this.isReconnect = true;
        } else if (state == 3) {
        //  重连成功
          this.$message.success('重连成功')
          this.isReconnect = false;
        }  else if (state == 5) {
        //  重连失败，重新退出白板界面
          this.$message.warning('重连失败，退出房间')
          console.log('重连失败，退出房间');
          this.isDelChecked = false;
          this.exitBoard()
        }

      });
      this.CanvasEventResize();
      // 默认曲线
      YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.FREELINE);
      this.controllerList.forEach(item => {
        if (item.className == 'brush') {
          item.isActive = 'active';
        } else if (item.className == 'lock' && this.isLock) {
          item.isActive = 'active';
        } else {
          item.isActive = '';
        }
      });
      this.BruchBoxShow.list.forEach(item => {
        if (item.className == 'curve') {
          item.isActive = 'active';
        } else {
          item.isActive = '';
        }
      })

        //默认红色
        this.handleClickColor(this.ColorBoxShow.list[0], 0)
      YTX_WBSS_API.setLineWidth(3);
    },

    //渲染Canvas
    CanvasEventResize(){
      this.$nextTick(() => {
        YTX_WBSS_API.notifyCanvasEventResize(function() {});
      });
      // setTimeout(()=>{
      //   YTX_WBSS_API.notifyCanvasEventResize(function() {});
      // },1000)
    },

    //关闭所有closeBox
    closeAllBox() {
      ["BruchBoxShow", "ColorBoxShow", "LayoutBoxShow", "MoreBoxShow", 'DocBoxShow'].forEach(
        it => {
          this[it].isShow = false;
        }
      );
    },

    //控制条点击
    handleClickController(item, index) {
      this.closeAllBox();
      this.isWhiteBoardInput = false;
      if (item.children) {
        this[item.children].isShow = true;
      } else {
        switch (item.className) {
          case "laser":
            YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.LASERPEN);
            break;
          case "reset":
            YTX_WBSS_API.drawUndo(
              function(resp) {
                console.log("fun_drawUndo onSuccess");
              },
              function() {
                console.error("fun_drawUndo onError");
              }
            );
            break;
          case "save":
            this.save();
            break;
          case "context":
            this.isWhiteBoardInput = !this.isWhiteBoardInput;
            this.$nextTick(()=>{
              this.$refs.WhiteBoardInputRef.$el.style.top = "200px";
              this.$refs.WhiteBoardInputRef.$el.style.left = "200px";
            })
            break;
          case "eraser":
            this.eraser();
            break;
          case "close":
            this.$bus.emit('stopWhiteBoardSharing');
            break;
          case "exitWbss":
            this.$bus.emit('leaveWbRoom');
            break;
          case "mini":
            this.$bus.emit('changeWbSize', true)
            break;
          case "docShare":

            break;
          case "redo":
            YTX_WBSS_API.drawRedo(
              function(resp) {
                console.log("fun_drawRedo onSuccess");
              },
              function() {
                console.error("fun_drawRedo onError");
              }
            );
            break;
          case "delete":
            this.clearCurrentPage();
            break;
          case "lock":
            this.lockLabel();
            break;
          default:
            break;
        }
        this.showControllActive(item.className);
      }
    },
    showControllActive(className) {
      let arr = ['brush','laser', 'eraser'];
      if (arr.includes(className)) {
        this.controllerList.forEach(item => {
          if (arr.includes(item.className)) {
            item.isActive  = ''
          }
          if (item.className === className) {
            item.isActive = 'active';
          }
        });
        //当选中工具为画笔或者橡皮擦时，清空画笔的线条选中
        if (className === 'eraser' || className === 'laser') {
          this.BruchBoxShow.list.forEach(it => {
            it.isActive = '';
          })
        }
      } else if (className === 'lock') {
        this.controllerList.forEach(item => {
          if (item.className === 'lock') {
            if (this.isLock){
              item.isActive = 'active'
            } else {
              item.isActive = '';
            }
          }
        })
      }
    },
    shareDoc(docId, callback) {
      var builderShareDoc = new YTX_WBSS_API.ShareDocBuilder(this.wbRoomId, docId);
      console.log(builderShareDoc);
      this.DocBoxShow.isShow = false;
      YTX_WBSS_API.shareDoc(builderShareDoc, resp => {
        console.log("fun_shareDoc onSuccess resp=" , resp);
        this.current = resp.pageId;
        this.docId = docId;
        for (let i = 0; i < this.docArr.length; i++) {
          let doc = this.docArr[i];
          console.log('===doc.wbssDocId===', doc.wbssDocId);
          if (doc.wbssDocId == docId) {
            this.totalPage = doc.wbssDocTotalPage;
            break;
          }
        }
        callback && callback()
      }, function (resp) {
        console.log("fun_shareDoc onError resp=" , resp);
      });
    },
    addDoc() {
      if (this.docArr.length > 8) {
        this.$message.warning(this.$t('last.overFileMax'));
        return;
      }
      this.$refs.fileInput.click()
    },

    //选择画笔
    handleClickBruch(item, index) {
      this.showControllActive('brush');
      this.BruchBoxShow.list.forEach((it, i) => {
        it.isActive = "";
        if (index === i) {
          it.isActive = "active";
          switch (it.className) {
            case "beeline":
              YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.LINE);
              break;
            case "curve":
              YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.FREELINE);
              break;
            case "quadrangle":
              YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.RECT);
              break;
            case "trigon":
              YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.TRIANGLE);
              break;
            case "circle":
              YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.ELLIPSE);
              break;
            default:
              break;
          }
        }
      });
    },

    //选择字号
    handleClickFontSize(item, index) {
      this.LayoutBoxShow.fontSize.forEach((it, i) => {
        it.isActive = "";
        if (index === i) {
          it.isActive = "active";
          this.whiteBoardTextFontSize = Number(it.text);
        }
      });
    },

    //选择粗细
    handleClickFontWeight(item, index) {
      this.LayoutBoxShow.fontWeight.forEach((it, i) => {
        it.isActive = "";
        if (index === i) {
          it.isActive = "active";
          YTX_WBSS_API.setLineWidth(it.value);
        }
      });
    },

    //选择颜色
    handleClickColor(item, index) {
      this.ColorBoxShow.list.forEach((it, i) => {
        it.isActive = "";
        if (index === i) {
          // 修改主控制条颜色
          it.isActive = 'active';
          console.log('===it===', it);
          // 设置颜色
          // 224,32,32,1
          let builder = new YTX_WBSS_API.ColorBuilder(...it.rgb, 255);
          YTX_WBSS_API.setLineColor(builder);
        }
      });
    },

    // 清除
    clearCurrentPage() {
      this.$confirm(this.$t('last.clearTip'), this.$t('modelMessage.prompt'), {
        confirmButtonText: this.$t('btn.determine'),
        cancelButtonText: this.$t('btn.cancel'),
        showClose: false,
        showCancelButton: true,
        center: true,
        customClass: "confirm",
        // type: 'info'
      })
        .then(() => {
          YTX_WBSS_API.clearCurrentPage();
        })
        .catch(() => {});
    },

    //添加画板页码
    addPagation() {
      if (this.totalPage < 5) {
        this.totalPage = this.totalPage + 1;
      }
    },

    //画板上一页
    togglePrevPagation() {
      if (this.current > 1) {
        this.$parent.current = this.$parent.current -1;
        // this.current = this.current - 1;
        this.gotoPage(1);
      }
    },

    //画板下一页
    toggleNextPagation() {
      if (this.current < this.totalPage) {
         this.$parent.current = this.$parent.current +1;
        // this.current = this.current + 1;
        this.gotoPage(3);
      }
    },

    // 切换页码
    gotoPage(type, pageId) {
      // type : 1:上一页 2：指定到哪页 3：下一页
      if (type === 1) {
        YTX_WBSS_API.gotoPrevPage(
          function(resp) {
            console.log(resp);
          },
          function() {}
        );
      } else if (type === 2) {
        var builder = new YTX_WBSS_API.GotoPageBuilder();
        builder.setPageIndex(pageId);
        console.log("pageId=" + pageId);
        YTX_WBSS_API.gotoPage(builder, function(resp) {
          console.log(resp);
        });
      } else if (type === 3) {
        YTX_WBSS_API.gotoNextPage(
          function(resp) {
            console.log(resp);
          },
          function() {}
        );
      }
    },

    //删除画板
    delPagation() {
      if (this.totalPage > 1) {
        this.totalPage = this.totalPage - 1;
        // 删除当前页画板 需要修正当前页码
        if (this.current > this.totalPage) {
          this.current = this.totalPage;
        }
      }
    },

    // 标注锁
    lockLabel(value) {
      /**
       * 0：锁定 1：解锁  2：锁定“发起白板操作” 3：解锁 4：锁定“白板标注” 5：解锁
       *
       * **/
      let self = this;
      this.isLock = !this.isLock;
      let acon = this.isLock ? 4 : 5;
      let LockMeetBuilder = new RL_MEET.LockMeetBuilder();
      LockMeetBuilder.setAction(acon);
      LockMeetBuilder.setConfId(self.roomId);
       RL_MEET.LockMeet(
        LockMeetBuilder,
        res => {
          self.$message.info(this.$t('last.noteLock') + (this.isLock ? this.$t('last.open') : this.$t('btn.close')));
        },
        function(error) {
          console.info(error);
        }
      );
    },

    // 鼠标事件状态
    isMouseEventEnable(bool) {
      YTX_WBSS_API.setMouseEventEnable(bool);
      this.isLock = !bool;
    },

    // 画板保存
    save() {
      this.$confirm(this.$t('last.noteSave'), this.$t('modelMessage.prompt'), {
        confirmButtonText: this.$t('btn.determine'),
        cancelButtonText: this.$t('btn.cancel'),
        showClose: false,
        showCancelButton: true,
        customClass: "confirm",
        center: true
        // type: 'info'
      })
        .then(() => {
          let png_blob_file_url = YTX_WBSS_API.getCurrentPagePNG(
            function(resp) {
              if (resp.img_blob_url != null) {
                console.log("fun_save_pngfile onSuccess");
              }
            },
            function(resp) {
              console.error("fun_save_pngfile onError resp=" + resp);
            }
          );

          if (png_blob_file_url != null) {
            // 文件保存到本地
            let download_png_file_element = document.createElement("a");
            download_png_file_element.download = `${this.$t('last.wb') + this.current}.png`;
            download_png_file_element.href = png_blob_file_url;
            download_png_file_element.src = png_blob_file_url;
            download_png_file_element.style.display = "none";
            document.body.appendChild(download_png_file_element);
            download_png_file_element.click();
            this.$message.info(this.$t('settings.saveSucc'));
          }
        })
        .catch(() => {});
    },

    // 文本blur
    handlewhiteBoardInputBlur(e) {
      let y = parseInt(e.target.parentElement.style.top);
      let x = parseInt(e.target.parentElement.style.left);
      if (this.whiteBoardInput) {
        this.addText(
          x,
          y,
          this.whiteBoardInput,
          this.whiteBoardTextFontSize,
          res => {
            this.whiteBoardInput = "";
          }
        );
      }
      this.isWhiteBoardInput = false;
    },

    //绘制文本
    addText(x, y, text, fontSize, callback) {
      // 获取可视化区域,相对于Canvas布局
      var view_rect_info = YTX_WBSS_API.getCurrentPageViewRectFromCanvas();
      if (view_rect_info != null) {
        if (view_rect_info.width > 0 && view_rect_info.height > 0) {
          // 设置文本状态,不然点击屏幕会出现绘图
          try {
            YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.FONT);
            // 添加文本
            YTX_WBSS_API.addText(
              { x, y },
              text,
              fontSize,
              "wbss_fangzheng_font.ttf",
              function(resp) {
                console.log("fun_addText_test onSuccess");
                callback && callback(resp);
              }
              // function(resp) {
              //   console.error("fun_addText_test onError");
              // }
            );
          } catch (error) {}
        }
      }
    },

    // 橡皮擦
    eraser() {
      // let shapeType = YTX_WBSS_API.getLineShape();
      // if (
      //   shapeType != YTX_WBSS_API._shapeType.ERASER_DRAW_LINE &&
      //   shapeType != YTX_WBSS_API._shapeType.ERASER_DRAW_ID
      // ) {
        YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.ERASER_DRAW_LINE);
      // } else {
      //   if (shapeType == YTX_WBSS_API._shapeType.ERASER_DRAW_LINE) {
      //     YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.ERASER_DRAW_ID);
      //   } else {
      //     YTX_WBSS_API.setLineShape(YTX_WBSS_API._shapeType.ERASER_DRAW_LINE);
      //   }
      // }
    },
    changeSize(mini) {
      if (mini) {
        this.$refs.wbBox.style = 'width:1px;height:1px;overflow:hidden;';
      } else {
        this.$refs.wbBox.style = ''
      }
    },
    uploadDoc(e) {
      let file = e.target.files[0]
      console.log('===file===', file);
      YTX_WBSS_API.uploadDoc(file,
        function(resp) {
          console.log("uploadDoc onSuccess resp=" + resp)
        }, function(resp) {
          console.log("uploadDoc onError resp=" + resp)
        }, function(resp) {
          console.log("uploadDoc onProcess resp=" + resp)
        })
    }
  },
};
</script>

<style lang="scss" scoped>
// 定义控制器icon数组
$controllerArr: brush, laser, context, color, eraser, layout, reset, more, save,
  beeline, curve, quadrangle, trigon, close, exitWbss, mini, docShare, lock, exchange, redo, delete, circle;
// 定义颜色
$controllerColorArr: cE02020, cFA6400, cF7B500, c6DD400, c00EAE6, c0091FF,
  c6236FF, cFFFFFF;
$colorArr: color_red, color_orange, color_yellow, color_green, color_blue, color_purple,
color_gray, color_black;
.white-board-box {
  background: #ffffff;
  //   width: 100%;
  //   height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 999;
  pointer-events: auto;
  width: 100%;
  overflow:hidden;
  .controller {
    &.disabled {
      &::after {
        content: "";
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        border-radius: 4px;
        background: rgba(0, 0, 0, 0.7);
      }
    }
    background: #222222;
    border-radius: 20px;
    position: absolute;
    top: 48px;
    ul {
      padding: 10px 0;
      li {
        cursor: pointer;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 40px;
        height: 44px;
        border-radius: 4px;
        i {
          display: inline-block;
          margin-bottom: 4px;
          position: relative;
          left: 0;
          top: 0;
          width: 40px;
          height: 20px;
          @each $c in $controllerArr {
            &.#{$c} {
              width: 40px;
              height: 20px;
              background: url("../../assets/img/whiteboard/#{$c}.png") center;
              background-size: 100% auto;
            }
          }
          @each $ci in $controllerColorArr {
            &.#{$ci} {
              width: 40px;
              height: 20px;
              background-image: url("../../assets/img/whiteboard/#{$ci}.png");
              background-size: 100% auto;
            }
          }
        }
        i.mini{
          width: 18px;
          height: 18px;
        }
        span {
          color: #cccccc;
          font-size: 12px;
          -ms-user-select: none; /*IE10*/
          user-select: none;
          -webkit-user-select: none; /*webkit浏览器*/
        }
        &:hover,
        &.active {
          i {
            @each $c in $controllerArr {
              &.#{$c} {
                width: 40px;
                height: 20px;
                background-image: url("../../assets/img/whiteboard/#{$c}_active.png");
                background-size: 100% auto;
              }
            }
            @each $ci in $controllerColorArr {
              &.#{$ci} {
                width: 40px;
                height: 20px;
                background-image: url("../../assets/img/whiteboard/#{$ci}.png");
                background-size: 100% auto;
              }
            }
          }
        }
      }
    }

    .bruch-box {
      width: 405px;
      height: 65px;
      background: #222222;
      border-radius: 4px;
      position: absolute;
      top: 10px;
      left: 50px;
      ul {
        height: 65px;
        li {
          float: left;
          margin: 0 20px;
          &:hover,
          &.active {
            i{
              width: 24px;
              height: 24px;
              margin-bottom: 6px;
            }
          }
          i{
            width: 24px;
            height: 24px;
            margin-bottom: 6px;
          }
        }
      }
    }
    .color-box {
      width: 314px;
      height: 50px;
      background: rgba(32, 32, 32, 1);
      border-radius: 4px;
      position: absolute;
      top: 60px;
      left: 200px;
      ul {
        padding: 0 10px;
        display: flex;
        justify-content: space-between;
        li {
          cursor: pointer;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 24px;
          height: 28px;
          margin-top: 13px;
          margin-left: 0;
          margin-right: 0;
          i {
            display: inline-block;
            margin-bottom: 4px;
            transform: scale(0.8);
            @each $c in $controllerColorArr {
              &.#{$c} {
                width: 24px;
                height: 24px;
                background-image: url("../../assets/img/whiteboard/#{$c}.png");
              }
            }
            &.active {
              @each $c in $controllerColorArr {
                &.#{$c} {
                  width: 24px;
                  height: 24px;
                  background-image: url("../../assets/img/whiteboard/#{$c}_active.png");
                }
              }
            }
          }
          &:hover {
            background: transparent;
            @each $c in $controllerColorArr {
              &.#{$c} {
                width: 24px;
                height: 24px;
                background-image: url("../../assets/img/whiteboard/#{$c}.png");
              }
            }
          }
        }
      }
    }
    .layout-box {
      width: 182px;
      background: #222222;
      border-radius: 4px;
      position: absolute;
      top: 128px;
      left: 50px;
      .colorBox{
        width: 152px;
        height: 64px;
        margin: 19px auto 0;
        div{
          float: left;
          width: 38px;
          height: 32px;
          position: relative;
          display: block;
          i{
            position: absolute;
            left: -3.5px;
            top: -2.5px;
            width: 45px;
            height: 37px;
            box-shadow: 0px 2px 4px 0px rgba(0,0,0,0.5);
            display: none;
            z-index: 99;
            @each $c in $colorArr {
              &.#{$c} {
                background-image: url("../../assets/img/whiteboard/#{$c}.png");
                background-size: 100% auto;
              }
            }
          }
          &:hover, &.active{
            i{
              display: block;
            }
          }
          @each $ci in $colorArr {
            &.#{$ci} {
              background-image: url("../../assets/img/whiteboard/#{$ci}.png");
              background-size: 100% auto;
            }
          }
        }
      }
      div.second {
        color: rgba(255, 255, 255, 1);
        display: flex;
        align-items: center;
        justify-content: start;
        height: 34px;
        &.second {
          margin: 15px;
        }
        h6 {
          font-size: 12px;
          font-family: PingFangHK, PingFangHK-Regular;
          font-weight: 400;
          text-align: center;
          color: #ffffff;
        }
        p {
          display: flex;
          justify-content: center;
          align-items: center;
          margin-left: 10px;
          span {
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 4px;
            font-size: 20px;
            margin-right: 11px;
            padding: 4px;
            width: 23px;
            height: 23px;
            &.font-20 {
              font-size: 10px;
            }
            &.font-24 {
              font-size: 12px;
            }
            &.font-28 {
              font-size: 14px;
            }
            &.font-32 {
              font-size: 16px;
              padding: 6px;
            }
            &.font-48 {
              font-size: 24px;
              padding: 8px;
            }
            &.active {
              color: #333333;
              background: rgba(255, 255, 255, 1);
            }
          }
          i {
            display: flex;
            justify-content: center;
            align-items: center;
            border-radius: 5px;
            cursor: pointer;
            width: 6px;
            height: 6px;
            background: url("../../assets/img/whiteboard/fontweight.png");
            background-size: 100% auto;
            &.font-20 {
              width: 5px;
              height: 5px;
              margin-left: 16px;
            }
            &.font-24 {
              width: 11px;
              height: 11px;
              margin-left: 18px;
            }
            &.font-28 {
              width: 17px;
              height: 17px;
              margin-left: 15px;
            }
            &.font-32 {
              width: 12px;
              height: 12px;
              border-radius: 10px;
              margin-left: 33px;
            }
            &.font-48 {
              width: 14px;
              height: 14px;
              border-radius: 12px;
              margin-left: 33px;
            }
            &.active {
              background: url("../../assets/img/whiteboard/fontweight_active.png");
              background-size: 100% auto;
            }
          }
        }
      }
    }
    .more-box {
      background: #222222;
      border-radius: 4px;
      position: absolute;
      top: 10px;
      left: 50px;
      padding: 12px;
      display: flex;
      li{
        height: 55px;
        float: left;
        margin-right: 25px;
        font-size: 12px;
        font-family: PingFangHK, PingFangHK-Regular;
        font-weight: 400;
        text-align: center;
        color: #999999;
        line-height: 25px;
        &:nth-last-child(1){
          margin-right: 0;
        }
        div, img{
          width: 24px;
          height: 24px;
          border-radius: 3px;
          margin: 3px auto 0;
        }
        div{
          background: #fff;
        }
        span{
          display: block;
          width: 48px;
          text-align: center;
        }
      }
      li.add-doc{
        div{
          background: url("../../assets/img/whiteboard/doc-add.png");
          background-size: 100% auto;
        }
        &:hover{
          div{
            background: url("../../assets/img/whiteboard/doc-add_active.png");
            background-size: 100% auto;
          }
        }
      }
    }
  }

  .fonter {
    position: absolute;
    bottom: 18px;
    right: 22px;
    display: flex;
    justify-content: space-between;
    .text {
      font-size: 12px;
      color: #999999;
      margin: 0 5px;
      width: 50px;
      text-align: center;
    }
    i {
      // 定义控制器icon数组
      $controllerFooterArr: add, left, right, delete;
      cursor: pointer;
      display: inline-block;
      @each $c in $controllerFooterArr {
        &.#{$c} {
          width: 16px;
          height: 16px;
          background-image: url("../../assets/img/whiteboard/#{$c}.png");
        }
      }
      &:hover {
        @each $c in $controllerFooterArr {
          &.#{$c} {
            width: 16px;
            height: 16px;
            background-image: url("../../assets/img/whiteboard/#{$c}_active.png");
          }
        }
      }
    }
  }
  .whiteBoardInput {
    position: absolute;
    width: 200px;
    top: 200px;
    left: 200px;
    z-index: 2;
  }
  .white-board-zoom {
    position: absolute;
    top: 40px;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 1;
  }
}
  .mini{
    width: 30px;
    height: auto;
    position: absolute;
    right: 50px;
    top: 50px;
  }
  .whiteboard-header{
    width: 100%;
    height: 40px;
    border-radius: 8px 8px 0px 0px;
    text-align: center;
    .txt{
      font-size: 14px;
      font-family: PingFangHK, PingFangHK-Regular;
      font-weight: 400;
      text-align: center;
      color: #ffffff;
      line-height: 40px;
    }
    .wb-close{
      float: right;
      width:30px;
      height: 30px;
      color: #fff;
      margin-top: 10px;
      margin-right: 20px;
      font-size: 20px;
      cursor: pointer;
    }
  }
.confirmLeavepDialog .el-dialog__header {
  background: rgba(237, 237, 237, 1);
}
</style>
<style scoped lang="stylus">
  .whiteboard-header{
    background $mainColor
  }
  .white-board-box .controller ul li:hover span, .white-board-box .controller ul li.active span{
    color $mainColor
  }
</style>



