<template>
  <div>
    <el-dialog :title="$t('meeting.meetingDetail')" custom-class="meetInfoDialog" :visible.sync="isShow" style="line-height: 30px;"
      width="400px" :modal='false' :close-on-click-modal='false' v-dialogDrag :before-close="handleClose">
      <h4>{{$t('meeting.conferenceTheme')}}</h4>
      <p>{{roomInfo.confName}}</p>
      <h4>{{$t('last.confTopic')}} <span class="fr col2a5 cp" v-if="!edit&&isHost" @click="editTopicBtn()">{{$t('connect.edit')}}</span><span
          class="fr col2a5 cp" style="margin-left:10px;" v-if="edit" @click="saveTopicBtn">{{$t('last.compelete')}}</span>

      </h4>
      <ul style="margin-bottom: 60px;" v-if="!edit">
        <div class="emptyMeetTopic" v-if="confTopic&&confTopic.length==0">

          <p><img src="../../assets/img/base/nomeetConf.png" alt=""></p>
          <p>{{$t('last.createTopic')}}</p>
        </div>
        <li v-for="(item,index) in confTopic" :key="index">
          <p>{{item.topic}}</p>
        </li>

      </ul>
      <ul v-if="edit">

        <li v-for="(item,index) in editConfTopics" :key="index" style="margin-bottom:3px;">
          <el-row>
            <el-col :span="1">
              <!-- {{index+1}}. -->
            </el-col>
            <el-col :span="22">
              <el-input :placeholder="$t('last.chatTip')" maxlength="20" v-model.trim="item.topic" clearable>
              </el-input>
            </el-col>
            <el-col :span="1">
              <i class="el-icon-remove-outline fl colred" style="cursor: pointer;font-size:20px; margin-top:20px;margin-left:5px;"
                @click="editConfTopics.splice(index, 1)"></i>
            </el-col>
          </el-row>
        </li>

        <li class="tc" v-if="editConfTopics.length<3" @click="addTopic()">
          <el-row style="margin-top: 25px">
            <el-col :span="6" :offset="1" style="margin-left: 36%;">
              <p class="addTopic" style="cursor: pointer;margin-bottom:150px;">{{$t('last.addTopic')}}</p>
            </el-col>
          </el-row>
        </li>
      </ul>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    props: ['isShow', 'browserInfo', 'roomInfo', 'isHost'],
    data() {
      return {
        edit: false,
        editConfTopics: [],
        confTopic: []
      }
    },
    watch: {
      roomInfo(val) {
        if (val.confTopic) {
          this.confTopic = JSON.parse(val.confTopic).topics;
        }
      },
    },
    created() {

    },
    methods: {
      handleClose() {
        this.$parent.closeDialog('meetInfoDialog');
      },
      saveTopicBtn() {
        var self = this;
        for (let i = 0; i < self.editConfTopics.length; i++) {
          if (!self.editConfTopics[i].topic) {
            self.$message({
              message: this.$t('last.topicTip'),
              type: "error"
            });
            return
          }
        }
        let contentTopics = {
          topics: self.editConfTopics
        }

        var ConferenceUpdateBuilder = new RL_MEET.ConferenceUpdateBuilder();
        ConferenceUpdateBuilder.setConfTopic(JSON.stringify(contentTopics))
         RL_MEET.ConferenceUpdate(
          ConferenceUpdateBuilder,
          e => {
            if (e.statusCode == "000000") {
              self.$message({
                message: this.$t('last.changeTopic'),
                type: "success"
              });
              self.edit = false
            }
          },
          err => {
            console.log(err);
          }
        );

      },
      addTopic() {
        this.editConfTopics.push({
          topic: ''
        })
      },
      editTopicBtn() {
        this.editConfTopics = this.confTopic
        this.edit = true
      },
    }
  }
</script>

<style>
  .meetInfoDialog .el-dialog__header {
    font-size: 16px;
    text-align: center;
    font-weight: bold;
    color: #333;
    background: #ededed;
    height: 52px;
    line-height: 52px;
    padding: 0;
  }
  .meetInfoDialog .el-dialog__body {
    padding-top: 20px;
    height: 400px;
  }

  .meetInfoDialog .el-dialog__body h4 {
    color: #999;
    font-size: 12px;
    font-family: '微软雅黑';
    font-weight: 400;
  }

  .meetInfoDialog .el-dialog__body h4:nth-of-type(2){
    margin-top: 15px;
  }

  .meetInfoDialog .el-dialog__body p {
    color: #333;
    font-size: 16px;
    font-family: '微软雅黑';
    font-weight: 400;
  }

  .meetInfoDialog .emptyMeetTopic {
    text-align: center;
  }

  .meetInfoDialog .emptyMeetTopic img {
    margin-top: 58px;
    width: 100px;
    height: 100px;
  }

  .meetInfoDialog .emptyMeetTopic p:nth-child(2) {
    color: #999;
    font-size: 12px;
  }

  .meetInfoDialog .el-input__inner {
    border: 0;
    border-bottom: 1px solid #e0e0e0;
    padding-left: 0px;
    font-size: 16px;
    color: #333;
  }

  .meetInfoDialog .el-input__icon {
    display: none;
  }

  .meetInfoDialog .icon-baiban-suoxiao {
    margin-top: 2px;
  }

  .meetInfoDialog .content {
    color: #333;
  }
</style>

<style lang="stylus" scoped>
  .meetInfoDialog >>> .el-row .addTopic  {
    border: 1px solid $mainColor;
    color: $mainColor;
    font-size: 16px;
    border-radius: 5px;
  }
</style>



