<template>
  <el-dialog
    :title="$t('meeting.setMainVenue')"
    :visible.sync="isShow"
    center
    width="500px"
    :modal="false"
    :close-on-click-modal="false"
    :modal-append-to-body="false"
    v-dialogDrag
    custom-class="merberDialog"
    :before-close="handleClose"
  >
    <div class="userList">
      <ul class="main-hall">
        <li v-for="user in memberInObj"
            :key="'hostLiBg'+user.memberId">
          <el-row type="flex" align="middle">
            <el-col :span="4">
              <img :src="user.photoUrl?user.photoUrl:defaultImg" @error="noImgFind" alt/>
            </el-col>
            <el-col :span="10" class="tl">
              <p>{{user.userName}} <span v-if="user.memberId" v-show="IsHost(user.roleId)">(主持人)</span></p>
              <p>{{user.memberId | getTerminalUA}}</p>
            </el-col>
            <el-col :span="5" class="tr IconBg">
              <div class="IconDivBg" v-show="user.isOpenAudio">
                <svg class="icon" aria-hidden="true">
                  <use xlink:href="#RSIcon-kaiqimaikefeng1"></use>
                </svg>
              </div>
              <i class="RSIcon RSIcon-kaiqishexiangtou colaf84" v-show="user.isOpenVideo"
                 style="font-size:35px;"></i>
              <i class="RSIcon RSIcon-guanbishexiangtou colred" v-show="!(user.isOpenVideo)"
                 style="font-size:35px;"></i>
            </el-col>
            <el-col :span="5" class="tr IconBg">
              <el-checkbox v-model="user.memberId === mainHall" @change="checkChange($event, user)"></el-checkbox>
            </el-col>
          </el-row>
        </li>
      </ul>
    </div>
    <span slot="footer" class="dialog-footer">
            <el-button @click="handleClose">{{$t('btn.cancel')}}</el-button>
            <el-button style="margin-left:30px;" type="primary" @click="setMainHallEvent">{{$t('btn.determine')}}</el-button>
        </span>
  </el-dialog>
</template>

<script>
  import defaultImg from "../../assets/img/auth/defaultAvatar.png"; //默认头像
  import {mapGetters} from "vuex";

  export default {
    props: ['isShow', 'SelfMeetUserId'],
    data() {
      return {
        defaultImg: defaultImg,
        mainHall: '',
      }
    },
    computed: {
      ...mapGetters(['memberInObj', 'meetInfo', 'mainVenue'])
    },
    watch: {
      mainVenue(value) {
        this.mainHall = value;
      }
    },
    mounted() {
      if (this.mainVenue) {
        this.mainHall = this.mainVenue;
      }
      console.log('this.mainVenue===', this.mainVenue)
    },
    methods: {
      checkChange(e, user) {
        console.log('===e', e, user);
        if (e) {
          this.mainHall = user.memberId;
        } else {
          this.mainHall = '';
        }
      },
      handleClose() {
        this.$parent.closeDialog('setMainHallDialog');
      },
      setMainHallEvent() {
        let data = {
          LayoutIdx: "1_3",
          MainVenue: this.mainHall,
          confId: this.meetInfo.confId
        }
        RL_MEET.SetConfLayout(
          data,
          res => {
            this.handleClose();
            let resp = JSON.parse(res.result);
            if (resp.statusCode == '000000') {
            } else {
              this.$message.error(resp.statusMsg);
            }
          },
          function (error) {
            console.info(error);
          }
        );
      },
      noImgFind(event) {
        console.log("未正确加载到图片;");
        var img = event.srcElement;
        img.src = this.defaultImg;
        img.οnerrοr = null; //控制不要一直跳动
      },
    }
  }
</script>

<style lang="scss" scoped>
  .merberDialog {
    max-height: 600px;
    //   height: calc(100% - 800px);
    // background: rgba(0, 0, 0, 0.3);
    // border: 10px solid white;
    // border-radius: 5px;

    p {
      color: #333333;
      // text-align: center;
    }

    .IconDivBg {
      display: inline-block;
      width: 24px;
      height: 24px;
      margin-right: 18px;
      margin-left: -23px;

      .icon {
        width: 22px;
        height: 22px;

        vertical-align: middle;
        fill: currentColor;
        overflow: hidden;

      }
    }
    .el-dialog__header {
      font-size: 16px;
      text-align: center;
      font-weight: bold;
      color: #333;
      background: #ededed;
      height: 52px;
      line-height: 52px;
      padding: 0;
    }
  }

  .userList {
    // height: calc(100% - 800px);
    height: calc(100vh - 400px);
    overflow-y: auto;

    ul {
      li {
        padding: 10px 0;
        line-height: 22px;
        padding: 10px 20px;
        padding-right: 10px;

        &:hover {
          background: rgba(255, 255, 255, 0.1);
        }

        img {
          width: 45px;
          height: 45px;
          border-radius: 5px;
        }

        .IconBg {
          vertical-align: middle;

          i {
            vertical-align: middle;
            outline: none;
          }
        }

        .meetContet {
          // // height: 50px;
          //   vertical-align: middle;
          padding-bottom: 10px;

          padding-right: 15px;

          .isHoverShow {
            display: none;
          }

          i {
            // cursor: pointer;
          }

          i:nth-child(2) {
            margin-right: 10px;
          }
        }

        &:hover .isHoverShow {
          display: inline-block;
        }

        &:hover .isHoverHide {
          display: none;
        }
      }
    }
  }

  .el-dropdown {
    color: white;
  }

  .el-dropdown-menu {
    background: #000000;
    color: white;
    border: none;
  }

  .el-dropdown-menu__item {
    color: white;
  }

  .el-dropdown-menu__item:focus,
  .el-dropdown-menu__item:not(.is-disabled):hover {
    background: rgba(28, 28, 28, 1);
    color: white;
  }
</style>
<style>
  .merberDialog {
    /* z-index: 0; */
  }

  .merberDialog .el-drawer__wrapper {
    /* z-index: 0 !important; */
    float: right;
    width: 400px;
    left: auto;
    right: 0;
    height: calc(100vh - 120px);
    top: 50px;
    bottom: 0;
  }

  .merberDialog .el-drawer {
    width: 400px !important;
    background: rgba(34, 34, 34, 0.95);
    color: white;
    /* margin-top: 50px; */
    /* z-index: 2003 !important; */
    /* padding-top: 50px; */
    /* text-align: center; */
  }

  .merberDialog .el-drawer__header {
    padding-bottom: 0;
    margin-bottom: 0;
    padding: 15px;
    background: rgba(34, 34, 34, 1);
  }

  .merberDialog .el-drawer__header > :first-child {
    text-align: center;
    color: white;
    font-size: 18px;
    outline: none;
    /* font-weight: bold; */
    margin-left: 15px;
  }

  .merberDialog .el-drawer__close-btn {
    color: white;
    outline: none;
  }

  .main-hall .el-tabs__item {
    width: 200px;
    /* width: 0px; */
    height: 40px;
    padding: 0px;
    text-align: center;
    color: #FFFFFF;

  }

  .main-hall .el-tabs__nav-wrap::after {
    /* background: #FFFFFF; */
  }

  .main-hall .el-tabs__item.is-active {
    color: white;
  }

  .main-hall .el-tabs__item:hover {
    color: white;
  }

  .main-hall .el-tabs__active-bar {
    background: white;
  }

  .main-hall .el-tabs__nav-wrap::after {
    background: rgba(255, 255, 255, 0.3);
  }

  .MuteBtnBg {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0px;
  }
</style>



