<template>
    <div class="UserVideoBg">
        <video ref='userRef' autoplay muted v-show="user.isOpenVideo" class="self-video"></video>

        <img :src="memberInObj[userId] && memberInObj[userId].photourl?memberInObj[userId].photourl:defaultImg" v-show="!user.isOpenVideo" ondragstart="return false;" @error="noImgFind" alt />

        <speakstaus :user="user"></speakstaus>
    </div>
</template>

<script>
    import speakstaus from './speakstaus'
    import defaultImg from '../../assets//img/auth/defaultAvatar.png'
    import {mapGetters} from 'vuex'
    export default {
        props: ['userId'],
        data() {
            return {
                defaultImg: defaultImg,
            }
        },
        computed: {
            ...mapGetters(['memberInObj', 'streamObj', 'streamIdObj']),
            user() {
                if (this.userId && this.$store.getters.getMemberInById(this.userId)) {
                    let user = this.$store.getters.getMemberInById(this.userId);
                    return user
                } else {
                    return {}
                }
            }
        },
        components: {
            speakstaus
        },
        watch: {
            streamIdObj: {
                deep: true,
                handler(newVal, oldVal) {
                    console.log('===streamIdObj===' + this.userId);
                    if (this.userId && this.streamObj[this.userId] && newVal[this.userId] !== oldVal[this.userId]) {
                        this.videoShow(this.streamObj[this.userId]);
                    }
                }
            }
        },
        mounted() {

        },
        methods: {
            videoShow(stream) {
                if (this.$refs['userRef'] && stream){
                    this.$refs['userRef'].srcObject = stream;
                    console.log('self-渲染视频流=====', this.$refs['userRef'])
                    console.log('self-stream=====', this.$refs['userRef'].srcObject)
                }
            },
            noImgFind(event) {
                console.log("未正确加载到图片;");
                var img = event.srcElement;
                img.src = this.defaultImg;
                img.οnerrοr = null; //控制不要一直跳动
            },
            findUserInPub(userId) {

            },
        }
    }
</script>

<style lang="scss" scoped>
    .UserVideoBg {
        width: 100%;
        height: 100%;
        position: relative;

        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        img {
            width: 150px;
            height: 150px;
            position: absolute;
            left: 50%;
            margin-left: -75px;
            top: 50%;
            margin-top: -75px;
        }
    }
</style>



