<template>
  <div class="headerBg">
    <header>
      <el-row type="flex" :align="'middle'">
        <el-col :span="8">
          <!-- 功能缺失 此功能暂时屏蔽 -->
          <!-- <el-tooltip class="item" effect="dark"
            :content="`上行：${netStatus.uploadBitrate}；下行：${netStatus.downloadBitrate}；丢包率：${netStatus.packetLost}%`"
            placement="bottom-start">
            <button style="border:none;background:rgba(0,0,0,0)">
              <svg class="icon" v-show="netStatus.packetLost==0" aria-hidden="true">
                <use xlink:href="#RSIcon-xinhao-1"></use>
              </svg>
              <svg class="icon" aria-hidden="true" v-show="netStatus.packetLost!=0&&netStatus.packetLost!=100">
                <use xlink:href="#RSIcon-xinhao-2"></use>
              </svg>
              <svg class="icon" aria-hidden="true" v-show="netStatus.packetLost==100">
                <use xlink:href="#RSIcon-xinhao-"></use>
              </svg>
            </button>

          </el-tooltip> -->

          <span class="meetTime">{{ meetTimeHour + ":" + meetTimeMinute + ":" + meetTimeSecond}}</span>
        </el-col>

        <el-col :span="8" class="tc">
          <span>{{roomInfo.confName}}    {{ $t('meeting.meetingNumber') }}:{{roomInfo.confId}}</span>
        </el-col>
        <el-col :span="8" class="tr">
          <el-tooltip effect="dark" :content="isHost?$t('meeting.leaveEndTheMeeting'):$t('group.leaveMeet')" placement="top">
            <div class="CLoseBtnBg" @click="leaveMeet ">
              <i class="RSIcon RSIcon-guaduan" style="font-size:28px;"></i>
            </div>
          </el-tooltip>
          <el-tooltip effect="dark" placement="top" :content="$t('meeting.minimizeTheWindow')" v-if="isLogin">
            <div class="minimizePage" @click="minimizePage">
              <i class="iconfont icon-zuidahua" style="font-size:28px;"></i>
            </div>
          </el-tooltip>
          <el-tooltip effect="dark" :content="$t('meeting.whiteSharing')" placement="top" v-if="showShareBtn">
            <div class="screen-share" @click="joinWbRoom">
              <i class="el-icon el-icon-s-platform" style="font-size:28px;"></i>
            </div>
          </el-tooltip>
        </el-col>
      </el-row>
    </header>
    <div class="timerTip" v-if="timerTips" >
      {{ $t('meeting.ApproachingMeetingEndTime') }}{{roomInfo.duration*60 - meetingTime}}{{ $t('collection.seconds') }}
      <i class="el-icon-close" @click="timerTips= false;isShowTime=true"></i>
    </div>

  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    props: ['netStatus', 'roomInfo', 'isHost','isLogin', 'showShareBtn'],
    data() {
      return {
        meetingTime: 0,
        meetTimeHour: 0,
        meetTimeMinute: 0,
        meetTimeSecond: 0,
        timerTips: false,
        isShowTime: false
      }
    },
    computed: {
      ...mapGetters([
        'user_info'
      ])
    },
    watch: {
      roomInfo(val) {
        let time = new Date().getTime() + (parseInt(this.user_info.timeDifference) || 0);
        console.log('===time===', time, this.user_info.timeDifference);
        this.meetingTime = (parseInt((time - new Date(val.startTime).getTime()) /
            1000) < 0) ?
          0 : (parseInt((time - new Date(val.startTime).getTime()) / 1000));
      },
    },
    created() {
      this.autoTime();
      // console.log(this.roomInfo)


    },
    methods: {
      showTimerTips () {
        this.timerTips = true;
      },
      joinWbRoom() {
        this.$parent.joinWhiteBoardRoom(Number(this.$parent.whiteBoardRoomId), this.$parent.whiteBoardUserId)
      },
      autoTime() {
        setInterval(() => {
          this.meetingTime = this.meetingTime + 1;
          this.meetTimeHour =
            parseInt(this.meetingTime / 3600) > 9 ?
            parseInt(this.meetingTime / 3600) :
            "0" + parseInt(this.meetingTime / 3600);
          this.meetTimeMinute =
            parseInt((this.meetingTime - this.meetTimeHour * 3600) / 60) > 9 ?
            parseInt((this.meetingTime - this.meetTimeHour * 3600) / 60) :
            "0" +
            parseInt((this.meetingTime - this.meetTimeHour * 3600) / 60);
          this.meetTimeSecond =
            this.meetingTime -
            this.meetTimeHour * 3600 -
            this.meetTimeMinute * 60 >
            9 ?
            this.meetingTime -
            this.meetTimeHour * 3600 -
            this.meetTimeMinute * 60 :
            "0" +
            (this.meetingTime -
              this.meetTimeHour * 3600 -
              this.meetTimeMinute * 60);

          // var endTimeTip = this.roomInfo.duration * 60 - this.meetingTime

          // if (!this.isShowTime) {
          //   if (endTimeTip <= 180&&endTimeTip>0) {
          //     this.timerTips = true;
          //   }else{
          //      this.timerTips = false;
          //   }
          //
          // }

        }, 1000);

      },
      leaveMeet() {
        this.$parent.LeaveMeetBtn()
      },
      minimizePage(){
        this.$bus.emit('fullscreen')
        this.$bus.emit("updateMeetingList");  
      }
    }
  }
</script>

<style lang="stylus" scoped>
  .headerBg {
    width: 100%;
    position: absolute;
    left: 0;
    top: 20;
    z-index: 10;
  }
  header {
    height: 50px;
    line-height: 50px;
    background-color: rgba(0, 0, 0, 0.7);
    width: 100%;
    color: white;

    vertical-align: middle;

    .meetTime {
      margin-left: 10px;
      vertical-align: middle;
    }
    .Title {
      display: inline-block;
      text-align: center;
      margin: 0 auto;
    }
    .CLoseBtnBg {
      width: 80px;
      height: 50px;
      background: rgba(207, 80, 80, 1);
      text-align: center;
      float: right;
      cursor: pointer;

      &:hover {

        background: rgba(221, 93, 93, 1);
      }
    }
    .minimizePage {
      width: 100px;
      height: 50px;
      background: $btnNormalColor;
      text-align: center;
      float: right;
      cursor: pointer;
      margin-right: 1px;
      &:hover {
        background: $btnHoverColor;
      }
    }
  }

  .timerTip {
    position: fixed;
    bottom: 140px;
    left: 0px;
    height: 36px;
    line-height: 36px;
    padding-left: 15px;
    box-sizing: border-box;
    width: 200px;
    background: rgba(42, 84, 181, 1);
    border-radius: 4px;
    opacity: 0.8;
    color: #fff;
    font-size: 14px;
    z-index: 15;
  }

  .el-icon-close {
    cursor: pointer;
  }
  .screen-share{
    width: 100px;
    height: 50px;
    background: $btnNormalColor;
    text-align: center;
    float: right;
    cursor: pointer;
    margin-right: 1px;
    &:hover {
      background: $btnHoverColor;
    }
    i{
      line-height: 50px;
    }
  }
</style>
<style>
  .icon {
    margin-left: 20px;
    /* float: left; */
    /* margin-top: 10px; */
    vertical-align: middle;
    width: 20px;
    height: 20px;
    /* vertical-align: -0.15em; */
    fill: currentColor;
    overflow: hidden;
  }
</style>



