  KNoteMemberJoin: 2,
  KNoteMemberQuit: 3,
  KNoteKickMember: 4,
  KNoteUpdateMemberInfo: 5,
  KNoteInviteMember: 6, //会议邀请的通知
  KNoteInviteResult: 7,
