<template>
    <div class="UserVideoBg">
        <video ref='selfvideo' autoplay muted v-show="user.isOpenVideo"></video>
        <!--<span style="position:absolute;top:16px;left:0;color:#ffffff;z-index: 999;">{{userId}}</span>-->
        <img :src="memberInObj[userId]&&memberInObj[userId].photourl?memberInObj[userId].photourl:defaultImg" v-show="!user.isOpenVideo"
            ondragstart="return false;" @error="noImgFind" alt />
        <speakstaus :user="user" :MerberiList='$parent.MerberiList'></speakstaus>
    </div>
</template>

<script>
    import speakstaus from './speakstaus'
    import defaultImg from '../../assets/img/auth/defaultAvatar.png'
    import {mapGetters} from 'vuex'
    export default {
        props: ['userId'],
        data() {
            return {
                defaultImg: defaultImg
            }
        },
        computed: {
            ...mapGetters(['memberInObj', 'streamIdObj', 'streamObj']),
            user() {
                if (this.userId && this.$store.getters.getMemberInById(this.userId)) {
                    let user = this.$store.getters.getMemberInById(this.userId);
                    console.log('getMemberInById-----',JSON.stringify(user));
                    return user
                } else {
                    return {}
                }
            },
            stream() {
                if (this.userId && this.$store.getters.getStreamById(this.userId)) {
                    let stream = this.$store.getters.getStreamById(this.userId);
                    return stream
                } else {
                    return null
                }

            }
        },
        components: {
            speakstaus
        },
        watch: {

            userId(NewID, OldID) {
                console.log('userId----watch',NewID, OldID);
                this.$refs['selfvideo'].srcObject = null;
                this.bus.$off('videoShow' + OldID);
                 this.init(NewID);
                // }
            },
            streamIdObj: {
                deep: true,
                handler(newVal, oldVal) {
                    console.log('===streamIdObj===' + this.userId);
                    if (this.userId && this.streamObj[this.userId] && newVal[this.userId] !== oldVal[this.userId]) {
                        this.videoShow(this.streamObj[this.userId]);
                    }
                }
            }

        },
        mounted() {
            console.log(this.userId, "mounted====注册bus");
            this.init(this.userId)
        },
        methods: {
            init(userId) {
                setTimeout(() => {
                    if (this.stream) {
                        console.log('已存在视频流，直接渲染');
                        this.videoShow(this.stream);
                    }
                },100);
            },
            videoShow(stream) {
                if (this.$refs['selfvideo'] && stream && stream.id) {
                    this.$refs['selfvideo'].srcObject = stream;
                    console.log('video----', this.$refs['selfvideo']);
                    console.log(this.$refs['selfvideo'].srcObject,"渲染视频流,id:" + this.userId + ",userName:" + this.user.userName);
                }
            },
            noImgFind(event) {
                console.log("未正确加载到图片;");
                var img = event.srcElement;
                img.src = this.defaultImg;
                img.οnerrοr = null; //控制不要一直跳动
            },
        }
    }
</script>

<style lang="scss" scoped>
    .UserVideoBg {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;
        video {
            width: 100%;
            height: 100%;
            object-fit: cover;
        }

        img {
            width: 150px;
            height: 150px;
            position: absolute;
            left: 50%;
            margin-left: -75px;
            top: 50%;
            margin-top: -75px;
        }
    }
</style>



