<template>
  <div>
    <el-dialog :title="$t('last.meetDelay')" :visible.sync="isshow" width="500px" :modal='false'
      :close-on-click-modal='false' v-dialogDrag :before-close="handleClose"
      custom-class="delayDialog">
      <!-- :before-close="handleClose" -->
      <p class="prolongTips"><span style="color: #FF3B3B">{{$t('last.tenMinu')}}</span>{{$t('last.delayTip')}}</p>
      <el-radio-group @change="prolongTimesChange" v-model="prolongTimeRadio">
        <el-radio :label="0">{{$t('last.noDelay')}}</el-radio>
        <el-radio :label="15" :disabled="delayDisabled15">{{$t('last.minu15')}}</el-radio>
        <el-radio :label="30" :disabled="delayDisabled30">{{$t('last.minu30')}}</el-radio>
        <el-radio :label="45" :disabled="delayDisabled45">{{$t('last.minu45')}}</el-radio>
        <el-radio :label="60" :disabled="delayDisabled60">{{$t('last.minu60')}}</el-radio>
      </el-radio-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">{{$t('btn.cancel')}}</el-button>
        <el-button type="primary" @click="prolongMeet()">{{$t('btn.determine')}}</el-button>
      </span>
    </el-dialog>



  </div>
</template>

<script>
  export default {
    props: ['isshow', 'isHostT','willNextEndTime'],
    data() {
      return {
        prolongTimes: 15,
        delayDisabled15: false,
        delayDisabled30: false,
        delayDisabled45: false,
        delayDisabled60: false,
        prolongTimeRadio: 15,

        tryMinDuration: 0,
        tryMaxDuration: 0,
        nextEndTime:0
      }
    },
    watch: {
      isHostT(val) {
        this.isHost = val
      },
      willNextEndTime(val){
          this.nextEndTime=val
      }
    },
    methods: {
      handleClose() {
        this.$parent.closeDialog('prolongDialogVisible');
      },
      prolongTimesChange(val) {
        console.log('选择的会议延长时间', val)
        this.prolongTimes = val;
      },
      prolongMeet() {
        let self = this;
        if (self.prolongTimes == 0) {
         self.handleClose()
          self.$message.error(self.$t('last.showMeetTime'));
          // return;
        }
        // else {
          this.tryMinDuration = this.prolongTimes;
           RL_MEET.prolongMeetTime(this.tryMinDuration, this.tryMinDuration, e => {
              console.log(e, "会议延长时间设置成功");
               if (self.prolongTimes !== 0){
                 this.handleClose()
                 self.$message.success(this.$t('last.delaySuccess'));
               }
            },
            function (err) {
              console.log(err);
              self.$message.error(err.statusMsg);
            });
        // }
        // tryMinDuration tryMaxDuration
      },
      chooseDelayTime(nextEndTime) {
        var endTimes = new Date((nextEndTime).toString()).getTime();
        var startTimes = new Date((this.meetInfo.endTime).toString()).getTime();
        console.log(endTimes, startTimes, "startTimes")
        this.tryMaxDuration = (endTimes - startTimes) / (1000 * 60);
        // this.tryMaxDuration = 9;   // 测试disable的数据;
        if (this.tryMaxDuration > 0 && this.tryMaxDuration < 14) {
          // disabled
          this.delayDisabled15 = this.delayDisabled30 = this.delayDisabled45 = this.delayDisabled60 = true
        } else if (this.tryMaxDuration > 15 && this.tryMaxDuration < 29) {
          this.delayDisabled30 = this.delayDisabled45 = this.delayDisabled60 = true
        } else if (this.tryMaxDuration > 30 && this.tryMaxDuration < 44) {
          this.delayDisabled45 = this.delayDisabled60 = true
        } else if (this.tryMaxDuration > 45 && this.tryMaxDuration < 59) {
          this.delayDisabled60 = true
        }
      },
      ConferenceNotify(obj) {
        let self = this

        // 因为延长会议的通知走的是pushMsg; 所以单独做处理;
        if (obj.msgContent) {
          var delayMeetNoti = JSON.parse(obj.msgContent);
          if (delayMeetNoti.noteType == 26) {
            // 会议结束前的通知;
            console.log("会议结束前的通知", delayMeetNoti)
            self.chooseDelayTime(delayMeetNoti.nextEndTime);

          }
        }
      },
    }
  }

</script>

<style>
  .delayDialog.el-dialog{
    width: 450px!important;
  }
  .delayDialog.el-dialog .el-dialog__header {
    color: #333;
    font: bold 16px '苹方-港 中黑体','微软雅黑';
    height: 52px;
    line-height: 52px;
    text-align: center;
    background:rgba(237,237,237,1);
    padding: 0;
  }
  .delayDialog.el-dialog .el-dialog__body {
    padding: 24px 0 20px;
    background: #fff;
    margin-left: 50px;
  }
  .delayDialog.el-dialog .el-radio-group {
    text-align: left;
    /* margin-left: 50px; */
  }
  .delayDialog.el-dialog .el-radio {
    margin-right: 50px;
    margin-bottom: 20px;
  }
  .delayDialog.el-dialog .el-dialog__footer .el-button {
    padding: 10px 49px;
    font-size: 16px;
  }
</style>


<style lang="stylus" scoped>
  .prolongTips {
    font-weight: bold;
    margin-bottom: 15px;
    font-size: 16px;
    margin-bottom: 27px;
  }
  .delayDialog >>> .el-dialog__footer  {
    .el-button:nth-child(1){
      border-color: $btnNormalColor;
      color: $btnNormalColor;
    }
    .el-button:nth-child(2) {
      background: $btnNormalColor;
      color: #fff;
    }
  }
</style>





