<template>
  <el-dialog
    :title="$t('meeting.setRoundTour')"
    :visible.sync="isShow"
    center
    width="500px"
    :modal="false"
    :close-on-click-modal="false"
    :modal-append-to-body="false"
    v-dialogDrag
    custom-class="setRoundTourDialog"
    :before-close="handleClose"
  >
    <div class="roundTourCon">
      <ul>
        <li>
          <span class="text">{{$t('last.startRound')}}</span>
          <el-switch v-model="roundTour.state"  inactive-color="#dadada">
          </el-switch>
        </li>
        <li>
          <span class="text">{{$t('last.roundInterval')}}(s)</span>
          <el-select v-model="roundTour.roundTourInterval" placeholder="请选择">
            <el-option v-for="item in roundTourList" :key="item" :label="item" :value="item"></el-option>
          </el-select>
        </li>
        <li>
          <span class="text">{{$t('last.skipNoImgeUser')}}</span>
          <el-switch v-model="roundTour.skipNoImgeUser"  inactive-color="#dadada">
          </el-switch>
        </li>
      </ul>
    </div>
    <span slot="footer" class="dialog-footer">
        <el-button @click="handleClose">{{$t('btn.cancel')}}</el-button>
        <el-button style="margin-left:30px;" type="primary" @click="setRoundTourEvent">{{$t('btn.determine')}}</el-button>
    </span>
  </el-dialog>
</template>

<script>
  import {mapMutations} from "vuex";

  export default {
    props: ['isShow', 'SelfMeetUserId'],
    data() {
      return {
        roundTour:{
          state: false,
          skipNoImgeUser: false,
          roundTourInterval: 20,
        },
        roundTourList:[20,30,60,120,300],
      }
    },
    methods: {
      ...mapMutations(
        ['SET_MeetSetting']
      ),
      handleClose() {
        this.$parent.closeDialog('setRoundTourDialog');
      },
      setRoundTourEvent() {
        this.SET_MeetSetting(this.roundTour)
        this.handleClose();
      },
    }
  }
</script>

<style lang="stylus" scoped>
  .setRoundTourDialog {
    .el-dialog__header {
      font-size: 16px;
      text-align: center;
      font-weight: bold;
      color: #333;
      background: #ededed;
      height: 52px;
      line-height: 52px;
      padding: 0;
    }
  }
  .roundTourCon {
    height: calc(100vh - 600px);
    overflow-x:hidden;
    ul {
      li {
        height 40px
        display flex
        align-items center
        justify-content space-between
        .text {

        }
        .el-input {
          width auto
        }
        .el-input__inner {
          width 160px
          height 30px
          background #f0f0f0
          border-radius $borderRadiusSize
          border solid 1px #f0f0f0
        }
        .el-input__inner:focus {
          border solid 1px #7bc5b3
        }
        .el-select .el-input.is-focus .el-input__inner {
          border solid 1px #7bc5b3
        }
        .check {
          color $mainColor
          cursor pointer
        }
      }
    }
  }

  .el-dropdown {
    color: white;
  }

  .el-dropdown-menu {
    background: #000000;
    color: white;
    border: none;
  }

  .el-dropdown-menu__item {
    color: white;
  }

  .el-dropdown-menu__item:focus,
  .el-dropdown-menu__item:not(.is-disabled):hover {
    background: rgba(28, 28, 28, 1);
    color: white;
  }
</style>
<style>
  .setRoundTourDialog .el-drawer__wrapper {
    /* z-index: 0 !important; */
    float: right;
    width: 400px;
    left: auto;
    right: 0;
    height: calc(100vh - 120px);
    top: 50px;
    bottom: 0;
  }

  .setRoundTourDialog .el-drawer {
    width: 400px !important;
    background: rgba(34, 34, 34, 0.95);
    color: white;
    /* margin-top: 50px; */
    /* z-index: 2003 !important; */
    /* padding-top: 50px; */
    /* text-align: center; */
  }

  .setRoundTourDialog .el-drawer__header {
    padding-bottom: 0;
    margin-bottom: 0;
    padding: 15px;
    background: rgba(34, 34, 34, 1);
  }

  .setRoundTourDialog .el-drawer__header > :first-child {
    text-align: center;
    color: white;
    font-size: 18px;
    outline: none;
    /* font-weight: bold; */
    margin-left: 15px;
  }

  .setRoundTourDialog .el-drawer__close-btn {
    color: white;
    outline: none;
  }
  .MuteBtnBg {
    width: 100%;
    position: absolute;
    left: 0;
    bottom: 0px;
  }
</style>



