<template>
  <div class="userNameP ">
    {{user.userName}}
    <!-- <span v-if="MerberiList[user.memberId]" v-show="IsHost(MerberiList[user.memberId].roleId)">(主持人)</span> -->
    <span v-if="memberInObj[user.memberId]" v-show="IsHost(user.roleId)">({{$t('meeting.moderator')}})</span>

    <i class="RSIcon RSIcon-guanbimaikefeng colred " v-show="!user.isOpenAudio" style="font-size:18px;"></i>
    <span v-show="user.isOpenAudio" class="spanIconBg">
      <svg class="icon" aria-hidden="true">
        <use xlink:href="#RSIcon-kaiqimaikefeng1"></use>
      </svg>
      <!-- <svg class="icon" aria-hidden="true" v-show="user.audio.audioLevel<3">
        <use xlink:href="#RSIcon-kaiqimaikefeng5"></use>
      </svg>
      <svg class="icon" aria-hidden="true" v-show="user.audio.audioLevel>2&&user.audio.audioLevel<5">
        <use xlink:href="#RSIcon-kaiqimaikefeng7"></use>
      </svg>
      <svg class="icon" aria-hidden="true" v-show="user.audio.audioLevel>4&&user.audio.audioLevel<8">
        <use xlink:href="#RSIcon-kaiqimaikefeng6"></use>
      </svg>
      <svg class="icon" aria-hidden="true" v-show="user.audio.audioLevel>7&&user.audio.audioLevel<10">
        <use xlink:href="#RSIcon-kaiqimaikefeng1"></use>
      </svg> -->
    </span>
  </div>
</template>

<script>
  import {mapGetters} from 'vuex'
  export default {
    props: ['user'],
      computed: {
          ...mapGetters(['memberInObj'])
      }
  }
</script>
<style lang="scss" scoped>
  .userNameP {
    position: absolute;
    bottom: 0;
    left: 0;
    display: inline-block;
    padding: 5px 10px;
    color: white;
    background: rgba(0, 0, 0, 0.7);

    .icon {
      width: 20px;
      height: 20px;
    }

    .spanIconBg {
      float: right;
      display: inline-block;
      width: 40px;
      margin-left: -17px;
      margin-top: -2px;
    }
  }
</style>



