<template>
  <div>
    <el-dialog :title="$t('last.meetShare')" custom-class="meetShareDialog" :visible.sync="isshow" :modal='false' width="440px" v-dialogDrag :close-on-click-modal='false' :before-close="handleClose">
      <div >
        <p><span class="meetInfoTitle">{{$t('meeting.conferenceTheme')}}：</span><span class="meetInfoCon">{{ meetInfo.confName }}</span></p>
        <p><span class="meetInfoTitle">{{$t('last.meetTime')}}：</span><span class="meetInfoCon">{{ sts }} - {{ ets }}</span></p>
        <p><span class="meetInfoTitle">{{$t('last.confId')}}：</span><span class="meetInfoCon">{{ meetInfo.confId }}</span></p>
        <p><span class="meetInfoTitle">{{$t('last.confLink')}}：</span><span class="meetInfoCon">{{hostURL}}</span></p>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="handleClose" v-clipboard:copy="shareMessage"
          v-clipboard:success="onCopy" v-clipboard:error="onError">{{$t('last.copyShare')}}</el-button>
        <el-button class="copyLinkStyle" @click="meetShare = false,handleClose()" v-clipboard:copy="hostURL"
          v-clipboard:success="onCopy" v-clipboard:error="onError">{{$t('context.copyLink')}}</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
  export default {
    props: ['isshow', 'meetInfos'],
    watch: {

      meetInfos(val) {
        // this.hostURL = '';
        this.hostURL=window.location.href.split('?')[0].replace(/CloudMeet_New/,"connect");
        this.meetInfo = val;
        this.hostURL = this.hostURL + "?confId=" + this.meetInfo.confId + "&anonymous=1"
        if(this.meetInfo.startTime||this.meetInfo.reserveStartTime) {
          var st = (this.meetInfo.startTime||this.meetInfo.reserveStartTime) .replace(/-/g,"/").split(" ")[0];
          var sth = (this.meetInfo.startTime||this.meetInfo.reserveStartTime) .split(" ")[1].slice(0,5);
          var et = this.meetInfo.endTime.replace(/-/g,"/").split(" ")[0];
          var eth = this.meetInfo.endTime.split(" ")[1].slice(0,5);
          this.sts = st + " " + sth;
          this.ets = et + " " + eth;
        }
        this.shareMessage = `
          ${this.$t('meeting.conferenceTheme')}：${this.meetInfo.confName};
          ${this.$t('last.meetTime')}：${this.sts} - ${this.ets};
          ${this.$t('last.confId')}：${this.meetInfo.confId};
          ${this.$t('last.confLink')}：${this.hostURL}`
      }
    },
    data() {
      return {
        shareMessage: '',
        meetInfo: '',
        hostURL:"",
        sts: '',
        ets: '',
      }
    },
    created(){
      this.hostURL=window.location.href.split('?')[0].replace(/CloudMeet_New/,"connect");
    },
    methods: {
      handleClose() {
        this.$parent.closeDialog('meetShare');
      },
      onCopy(e) {
        this.$message.success(this.$t('last.copySuccess'), e);
      },
      onError(e) {
        this.$message.error(this.$t('last.copyFail'));
      },
    }
  }

</script>

<style lang="stylus" scoped>
.meetShareDialog >>> .copyLinkStyle.el-button{
  border: none;
  color: $btnNormalColor;
  &:hover{
    color: $btnHoverColor;
    background: #fff;
  }
}
.meetInfoTitle {
  display: inline-block;
  width: 80px;
  text-align:justify;
  text-align-last:justify;
  line-height: 30px;
  color: #999;
}
.meetInfoCon {
  color: #333;
}
</style>

<style>
  .meetShareDialog .el-dialog__header {
    font-size: 16px;
    text-align: center;
    font-weight: bold;
    color: #333;
    background: #ededed;
    height: 52px;
    line-height: 52px;
    padding: 0;
  }
  .meetShareDialog .el-dialog__footer {
    text-align: left;
  }
</style>



