<template>
	<div class="merberDialog">
		<el-drawer :title="$t('meeting.participant')" :visible.sync="isshow" direction="rtl" width="500px" :modal="false" :wrapperClosable='false'
			:before-close="handleClose"  v-drag>

			<el-tabs class="MerberListBg" v-model="active" v-if="memberInObj">
				<el-tab-pane :label="$t('last.memberIn') + '('+Object.getOwnPropertyNames(memberInObj).length+')'" name="1">
					<div class="userList">
						<ul>
							<li v-for="(user) in memberInObj"
								:key="user.memberId">
								<el-row type="flex" align="middle">
									<el-col :span="4">
										<div class="img">
											<span v-if="user.photourl">
												<img :src="user.photourl" alt="">
											</span>
											<span v-else :style="{backgroundImage:getcolor(user.account)}">{{user.userName | nameslice}}</span>
										</div>
										<!-- <img :src="user.photoUrl?user.photoUrl:defaultImg" @error="noImgFind" alt /> -->
									</el-col>
									<el-col :span="10" class="tl">
										<p>{{user.userName}} <span v-show="IsHost(user.roleId)">({{$t('meeting.moderator')}})</span></p>
										<p>{{user.memberId | getTerminalUA}}</p>
									</el-col>
									<el-col :span="10" class="tr IconBg">
										<!-- 屏幕共享 -->
										<i class="RSIcon RSIcon-gongxiang colaf84" v-if="user.state & 0x000008"
											style="font-size:40px;"></i>

										<!--举手-->
										<i class="RSIcon RSIcon-jushou colred"
											v-show="(user.state&0x00020000)&&user.roleId==11"
											style="font-size:20px;"></i>

										<!--麦克风-->
										<i class="RSIcon RSIcon-guanbimaikefeng colred" v-show="!user.isOpenAudio"
											style="font-size:22px;"></i>
										<div class="IconDivBg" v-show="user.isOpenAudio && user.memberId !== voiceIncentiveId">
											<svg class="icon" aria-hidden="true">
												<use xlink:href="#RSIcon-kaiqimaikefeng1"></use>
											</svg>
										</div>
										<i class="iconfont icon-yuyinzhengzaishuohua colaf84" v-show="user.isOpenAudio && user.memberId === voiceIncentiveId"></i>

										<!--摄像头-->
										<i class="RSIcon RSIcon-kaiqishexiangtou colaf84" v-show="user.isOpenVideo"
											style="font-size:35px;"></i>
										<i class="RSIcon RSIcon-guanbishexiangtou colred" v-show="!(user.isOpenVideo)"
											style="font-size:35px;"></i>

										<!--右侧下拉操作-->
										<el-dropdown v-show="(isHost || user.memberId === SelfMeetUserId)" class="hoverMenu isHoverShow fr">
											<span class="el-dropdown-link"> <i class="RSIcon RSIcon-gengduo" style="font-size:20px;"></i>
											</span>
											<el-dropdown-menu slot="dropdown">
												<el-dropdown-item v-if="user.isOpenAudio">
													<p @click="memberAudio(user,true)">
														{{$t('btn.close')}}<span v-show="user.memberId!=SelfMeetUserId">{{$t('connect.otherSide')}}</span>{{$t('meeting.microphone')}}
													</p>
												</el-dropdown-item>

												<el-dropdown-item v-if="(!user.isOpenAudio&&user.roleId!=11)">
													<p @click="memberAudio(user,false)">
														{{$t('last.open')}}<span v-show="user.memberId!=SelfMeetUserId">{{$t('connect.otherSide')}}</span>{{$t('meeting.microphone')}}
													</p>
												</el-dropdown-item>
												<el-dropdown-item v-if="user.isOpenVideo">
													<p @click="memberVideo(user,true)">
                            {{$t('btn.close')}}<span v-if="user.memberId!=SelfMeetUserId">{{$t('connect.otherSide')}}</span>{{$t('meeting.camera')}}
													</p>
												</el-dropdown-item>
												<el-dropdown-item v-if="!user.isOpenVideo">
													<p @click="memberVideo(user,false)">
                            {{$t('last.open')}}<span v-show="user.memberId!=SelfMeetUserId">{{$t('connect.otherSide')}}</span>{{$t('meeting.camera')}}
													</p>
												</el-dropdown-item>
												<!--<el-dropdown-item v-if="user.memberId===SelfMeetUserId">-->
													<!--<p @click="updateListUserName(user)">改名</p>-->
												<!--</el-dropdown-item>-->

												<!--<el-dropdown-item v-show="user.memberId!=SelfMeetUserId">-->
													<!--<p @click="addFriend(user)">添加至联系人</p>-->
												<!--</el-dropdown-item>-->
												<el-dropdown-item v-show="isHost&&SelfMeetUserId!=user.memberId && !IsHost(user.roleId)">
													<p @click="hostTransfer(user)">{{$t('last.setHoster')}}</p>
												</el-dropdown-item>
												<!--<el-dropdown-item v-if="false" v-show="isHost&&SelfMeetUserId!=user.memberId">-->
													<!--<p @click="YHkictout(user)">断开</p>-->
												<!--</el-dropdown-item>-->
												<el-dropdown-item v-show="isHost&&SelfMeetUserId!=user.memberId">
													<p @click="kictout(user)">{{$t('meeting.removedFromMeeting')}}</p>
												</el-dropdown-item>
											</el-dropdown-menu>
										</el-dropdown>
									</el-col>
								</el-row>
							</li>

						</ul>
					</div>
				</el-tab-pane>
				<el-tab-pane :label="$t('meeting.waitingForEnter')+'('+Object.getOwnPropertyNames(memberOutObj).length+')'"
							 v-if="Object.getOwnPropertyNames(memberOutObj).length > 0" name="3">
					<div class="userList">
						<ul>
							<li v-for="item in memberOutObj">
								<el-row type="flex" align="middle">
									<el-col :span="4">
										<div class="img">
											<span v-if="item.photourl">
												<img :src="item.photourl" alt="">
											</span>
											<span v-else :style="{backgroundImage:getcolor(item.account)}">{{item.userName | nameslice}}</span>
										</div>
										<!-- <img :src="item.photoUrl?item.photoUrl:defaultImg" @error="noImgFind" alt /> -->
									</el-col>
									<el-col :span="10" class="tl">
										<p>{{item.userName}}</p>
										<p>{{item.stateContent}}</p>
									</el-col>
									<el-col :span="10" class="tr IconBg">
									<el-dropdown v-show="isHost" class="hoverMenu isHoverShow fr">
									<span class="el-dropdown-link"> <i class="RSIcon RSIcon-gengduo" style="font-size:20px;"></i>
											</span>
										<el-dropdown-menu slot="dropdown">
											<el-dropdown-item v-show="isHost&&SelfMeetUserId!=item.memberId">
												<p @click="kictout(item)">{{$t('meeting.removedFromMeeting')}}</p>
											</el-dropdown-item>
										</el-dropdown-menu>
									</el-dropdown>
								</el-col>
								</el-row>

							</li>
						</ul>
					</div>
				</el-tab-pane>
				<el-tab-pane label="未静音"
					v-if="isHost&&false" name="2">
					<div class="userList">
						<ul>
							<li v-for="(user) in memberInObj" :key="'1'+user.memberId" v-show="(user.isOpenAudio)">
								<el-row type="flex" align="middle">
									<el-col :span="4">
										<div class="img">
											<span v-if="user.photourl">
												<img :src="user.photourl" alt="">
											</span>
											<span v-else :style="{backgroundImage:getcolor(user.account)}">{{user.userName | nameslice}}</span>
										</div>
										<!-- <img :src="user.photoUrl?user.photoUrl:defaultImg" @error="noImgFind" alt /> -->
									</el-col>
									<el-col :span="10" class="tl">
										<p>{{user.userName}} <span
												v-show="IsHost(user.roleId)">(主持人)</span>
										</p>
										<p>{{user.memberId | getTerminalUA}}</p>
									</el-col>
									<el-col :span="10" class="tr IconBg">
										<!-- 屏幕共享 -->
										<i class="RSIcon RSIcon-gongxiang colaf84" v-show="user.memberId==screenUserId"
											style="font-size:40px;"></i>
										<i class="RSIcon RSIcon-jushou colred"
											v-show="(user.state&0x00020000)&&user.roleId==11"
											style="font-size:20px;"></i>
										<i class="RSIcon RSIcon-guanbimaikefeng colred" v-show="!user.isOpenAudio"
											style="font-size:22px;"></i>

										<div class="IconDivBg" v-show="user.isOpenAudio">
											<svg class="icon" aria-hidden="true">
												<use xlink:href="#RSIcon-kaiqimaikefeng1"></use>
											</svg>
										</div>


										<i class="RSIcon RSIcon-kaiqishexiangtou colaf84" v-show="user.isOpenVideo"
											style="font-size:35px;"></i>
										<i class="RSIcon RSIcon-guanbishexiangtou colred" v-show="!(user.isOpenVideo)"
											style="font-size:35px;"></i>

										<el-dropdown v-show="(isHost||user.memberId==SelfMeetUserId)" class="hoverMenu isHoverShow fr">
											<span class="el-dropdown-link"> <i class="RSIcon RSIcon-gengduo" style="font-size:20px;"></i>
											</span>
											<el-dropdown-menu slot="dropdown">
												<el-dropdown-item v-show="(isHost||user.memberId==SelfMeetUserId)&&(user.isOpenAudio)">
													<!-- <p @click="memberAudio(user,true)">静音</p> -->
													<p @click="memberAudio(user,true)">
														关闭<span v-show="user.memberId!=SelfMeetUserId">对方</span>麦克风 </p>
												</el-dropdown-item>

												<el-dropdown-item v-if="user.memberId==SelfMeetUserId"
													v-show="(!user.isOpenAudio&&user.roleId!=11)">
													<!-- v-show="(isHost||user.id==SelfMeetUserId) &&(!(user.isOpenAudio))||(isHost||user.id==SelfMeetUserId)&&((MerberiList[user.id].state&0x00020000))" -->
													<p @click="memberAudio(user,false)">打开麦克风</p>
												</el-dropdown-item>

												<el-dropdown-item v-if="user.memberId!=SelfMeetUserId"
													v-show="(isHost||user.memberId==SelfMeetUserId) &&(!(user.isOpenAudio))||(isHost||user.memberId==SelfMeetUserId)&&((user.state&0x00020000))">
													<p @click="memberAudio(user,false)">
														打开<span v-show="user.memberId!=SelfMeetUserId">对方</span>麦克风 </p>
												</el-dropdown-item>
												<el-dropdown-item v-show="(isHost||user.memberId==SelfMeetUserId)&&(user.isOpenVideo)">
													<p @click="memberVideo(user,true)">关闭<span v-show="user.memberId!=SelfMeetUserId">对方</span>摄像头</p>
												</el-dropdown-item>
												<el-dropdown-item v-show="(isHost||user.memberId==SelfMeetUserId)&&!(user.isOpenVideo)">
													<p @click="memberVideo(user,false)">
														打开<span v-show="user.memberId!=SelfMeetUserId">对方</span>摄像头</p>
												</el-dropdown-item>
												<el-dropdown-item v-if="false" v-show="(isHost||user.memberId==SelfMeetUserId)">
													<p @click="updateListUserName(user)">改名</p>
												</el-dropdown-item>

												<el-dropdown-item v-if="false" v-show="user.memberId!=SelfMeetUserId">
													<p @click="addFriend(user)">添加至联系人</p>
												</el-dropdown-item>
												<el-dropdown-item v-show="user.memberId==screenUserId">
													<p @click="stopScreen(user)">关闭共享</p>
												</el-dropdown-item>
												<el-dropdown-item v-show="isHost&&SelfMeetUserId!=user.memberId">
													<p @click="hostTransfer(user)">设为主持人</p>
												</el-dropdown-item>
												<el-dropdown-item v-if="false" v-show="isHost&&SelfMeetUserId!=user.memberId">
													<p @click="YHkictout(user)">断开</p>
												</el-dropdown-item>
												<el-dropdown-item v-show="isHost&&SelfMeetUserId!=user.memberId">
													<p @click="kictout(user)">踢出会议</p>
												</el-dropdown-item>
											</el-dropdown-menu>
										</el-dropdown>
									</el-col>
								</el-row>
							</li>
						</ul>
					</div>
				</el-tab-pane>
			</el-tabs>

			<div class="MuteBtnBg" v-if="isHost">
				<el-row>
					<el-col :span="12" style="padding: 0px">
						<el-button type="primary" style="width:100%;border-right:1px solid white;  border-radius: 0;" class="tc"
							@click="allMuteClick">{{isAllmuet?$t('last.unMute'):$t('last.mute')}}</el-button>
					</el-col>
					<el-col :span="12" style="padding: 0px">
						<el-button type="primary" style="width:100%;  border-radius: 0;" class=" tc" @click="isAllCloseCameraClick">
							{{isAllCloseCamera?$t('last.unOpenCamera'):$t('last.closeCamera')}}</el-button>
					</el-col>
					<div class="cb"></div>
				</el-row>
			</div>

		</el-drawer>


		<el-dialog :title="$t('modelMessage.prompt')" :visible.sync="isMuteAllDialog" width="400px">
			<p>{{$t('last.confirmMute')}}</p>
			<!--<el-checkbox v-model="muteAllChecked" style="padding-top:10px;">禁止参会人自己解除静音</el-checkbox>-->
			<span slot="footer" class="dialog-footer">
				<el-button @click="isMuteAllDialog = false">{{$t('btn.cancel')}}</el-button>
				<el-button type="primary" @click="isMuteAll()">{{$t('btn.determine')}}</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import defaultImg from "../../assets/img/auth/defaultAvatar.png"; //默认头像
    import { mapGetters } from "vuex";
	export default {
		props: ['isshow', 'isHost', 'SelfMeetUserId',
			'screenUserId','voiceIncentiveId'],
		data() {
			return {
				defaultImg: defaultImg,
				merberList: [],
				merberCount: 0,
				active: '1',
				muteAllChecked: false,
				isMuteAllDialog: false,
                isAllmuet: false,
				isAllCloseCamera: false

			}
		},
        computed: {
            ...mapGetters(['memberInObj', 'meetInfo', 'memberOutObj'])
        },
        beforeDestroy() {
		    this.bus.$off('updateMediaStatus');
		},
		mounted() {
	        console.log('mounted---memberlist', this.meetInfo);
	        let joinState = this.meetInfo.joinState;
            console.log(joinState & 0x000040, joinState & 0x000004 == 0);
            if (joinState & 0x000040) {
                this.isAllmuet = false;
            } else {
                this.isAllmuet = true;
            }
            if (joinState & 0x000004) {
                this.isAllCloseCamera = false;
            } else {
                this.isAllCloseCamera = true;
            }
            this.bus.$on('updateMediaStatus', (content)=>{
                if (content === 'isOpenVideo' && this.isAllCloseCamera) {
                    // this.isAllCloseCamera = false;
				} else if (content === 'isOpenAudio' && this.isAllmuet) {
                    // this.isAllmuet = false;
				}
            });
		},
		methods: {
            isAllCloseCameraClick() {
                let action = '';
                let message = ''
                if (this.isAllCloseCamera) {
					action = 13;
                    message = this.$t('last.unOpenCamera') + this.$t('last.success')
				} else {
                    action = 12;
                    message = this.$t('last.closeCamera') + this.$t('last.success')
				}
                let self = this
                let mediaControlBuilder = new RL_MEET.MediaControlBuilder();
                mediaControlBuilder.setConfId(this.meetInfo.confId);
                mediaControlBuilder.setAction(action);
                mediaControlBuilder.setUnchangable(1);
                mediaControlBuilder.setAllMember(1);
                RL_MEET.MediaControl(
                    mediaControlBuilder,
                    e => {
                        console.log(e);
                        this.$message({
                            message
                        });
                        self.$parent.isAllCloseCamera = !self.isAllCloseCamera;
                        self.isAllCloseCamera = !self.isAllCloseCamera;
                    },
                    err => {
                        console.log(err);
                    }
                );

			},
		    allMuteClick() {
                this.isAllmuet?this.unmuteAll():this.isMuteAllDialog=true
			},
			hostTransfer(user) {
				this.$confirm(this.$t('btn.determine') + user.userName + this.$t('last.setHoster') + "？", this.$t('modelMessage.prompt'), {
					confirmButtonText: this.$t('btn.determine'),
					cancelButtonText: this.$t('btn.cancel'),
					// iconClass: "iconfont icon-danchuang-tanhao",
					customClass: "error",
					roundButton: true,
					center: true
				}).then(() => {
					let that = this;
					let transferModeratorBuilder = new RL_MEET.TransferModeratorBuilder();
					transferModeratorBuilder.setConfId(this.meetInfo.confId);
					transferModeratorBuilder.setMembers([{
						memberId: user.memberId,
						idType: 2
					}]);

					RL_MEET.TransferModerator(transferModeratorBuilder, e => {
						console.log(e)
						var resp = JSON.parse(e.result);
						if (resp.statusCode == '000000') {
							that.$message.success(user.userName + that.$t('last.beenHoster'));
						}
					})
				});

			},
			noImgFind(event) {
				console.log("未正确加载到图片;");
				var img = event.srcElement;
				img.src = this.defaultImg;
				img.οnerrοr = null; //控制不要一直跳动
			},
			kictout(user) {
				//踢人

				var name = user.userName;
				this.$confirm(this.$t('btn.determine') + name + this.$t('meeting.removedFromMeeting') + "？", this.$t('modelMessage.prompt'), {
					confirmButtonText: this.$t('btn.determine'),
					cancelButtonText: this.$t('btn.cancel'),
					// iconClass: "iconfont icon-danchuang-tanhao",
					customClass: "error",
					roundButton: true,
					center: true
				}).then(() => {
                    let kickMeetMemberBuilder = new RL_MEET.KickMeetMemberBuilder();
                    kickMeetMemberBuilder.setKickMembers([{
                        memberId: user.memberId,
                        idType: 2
                    }]);
                     RL_MEET.KickMeetMember(
                        kickMeetMemberBuilder,
                        e => {
                            console.log(e, "kick member success");
                        },
                        function (err) {
                            console.log(err);
                        }
                    );
				});
			},
			handleClose() {
				this.$parent.closeDialog('merberDialog');
			},
			memberAudio(member, Booleans) {
				let self = this;

				let memberId = member.memberId;
				if (memberId === self.SelfMeetUserId) {
					if (Booleans) {
						self.$parent.closeMicrophone();
					} else {
						self.$parent.openMicrophone(); //解除禁言
					}
					return;
				}
				if (!self.isHost) {
					return;
				}
				let mediaControlBuilder = new RL_MEET.MediaControlBuilder();

				if (Booleans) {
					mediaControlBuilder.setAction(2); //禁言
				} else {
					mediaControlBuilder.setAction(3); //解除禁言
				}
				mediaControlBuilder.setConfId(self.meetInfo.confId);
				mediaControlBuilder.setAllMember(0);
				mediaControlBuilder.setUnchangable(0);
				mediaControlBuilder.setMembers([{
					memberId: memberId,
					idType: 2
				}]);

				RL_MEET.MediaControl(
					mediaControlBuilder,
					e => {
						console.log(e);
						console.log("控制成员状态更新");
					},
					err => {
						console.log(err);
						console.log("控制成员状态更新失败");
					}
				);
			},
			kictoutBefore(user, index) {
				let _this = this;
				let kickMeetMemberBuilder = new RL_MEET.KickMeetMemberBuilder();
				kickMeetMemberBuilder.setKickMembers([{
					memberId: user.memberId,
					idType: 2
				}]);
				RL_MEET.KickMeetMember(
					kickMeetMemberBuilder,
					e => {
						console.log(e, "kick member success");
					},
					function (err) {
						console.log(err);
					}
				);
			},
			Reinviting(value) {
				//重新邀请
				console.log(value);

				let userlist = [{
					userName: value.userName,
					memberId: value.memberId,
					idType: 2
				}];
				let _this = this;
				let inviteMemberBuilder = new RL_MEET.InviteMemberBuilder();
				inviteMemberBuilder.setInviteMembers(userlist);
				RL_MEET.InviteMember(
					inviteMemberBuilder,
					e => {
						console.log("invite success ", e);
						_this.$message({
							message: _this.$t('last.reInviteTip'),
							type: "success"
						});
					},
					function (err) {
						console.log("invite error ", err);
						_this.$message({
							message: err.statusMsg,
							type: "error"
						});
					}
				);
			},
			YHkictout(user) {
				var name = user.userName;
				this.$confirm("您想让" + name + "断开会议？", this.$t('modelMessage.prompt'), {
					confirmButtonText: this.$t('btn.determine'),
					cancelButtonText: this.$t('btn.cancel'),
					type: "error"
				}).then(() => {
					var member = [{
						memberId: user.memberId,
						idType: 2
					}];
					RL_MEET.disconnectMember(member);
				});
			},


			memberVideo(member, Booleans) {
				// 摄像头控制
				let self = this;
				let memberId = member.memberId;
				if (memberId === self.SelfMeetUserId) {
					// 设置自己的摄像头状态
					if (Booleans) {
						self.$parent.closeCameraAndPubVideo();
					} else {
						self.$parent.openCamera(); //打开自己本地摄像头并推流
					}
					return;
				}
				if (!self.isHost) {
					return;
				}
				let mediaControlBuilder = new RL_MEET.MediaControlBuilder();
				mediaControlBuilder.setConfId(self.meetInfo.confId);
				mediaControlBuilder.setAllMember(0);
				mediaControlBuilder.setUnchangable(0);
				mediaControlBuilder.setMembers([{
					memberId: memberId,
					idType: 2
				}]);
				if (Booleans) {
					mediaControlBuilder.setAction(12);
				} else {
					self.$message.warning(this.$t('last.inviteOpenCamera'));
					mediaControlBuilder.setAction(13);
				}

				RL_MEET.MediaControl(
					mediaControlBuilder,
					e => {
						console.log(e);
					},
					err => {
						console.log(err);
					}
				);
				console.log(mediaControlBuilder, "mediaControlBuilder");
			},
			isMuteAll() {
				var unChangeable;
				if (this.muteAllChecked) {
					unChangeable = 1;
				} else {
					unChangeable = 0;
				}
				this.muteAll(unChangeable);

			},

			muteAll(val) {
				//静音
				var unChangeable;
				if (this.muteAllChecked) {
					unChangeable = 1;
				} else {
					unChangeable = 0;
				}
				let self = this
				let mediaControlBuilder = new RL_MEET.MediaControlBuilder();
				mediaControlBuilder.setConfId(self.meetInfo.confId);
				mediaControlBuilder.setAction(2);
				mediaControlBuilder.setAllMember(2);
				mediaControlBuilder.setUnchangable(unChangeable);
				RL_MEET.MediaControl(
					mediaControlBuilder,
					e => {
						console.log(e);
						self.$message({
							message: self.$t('last.muteTip')
						});
						self.$parent.isAllmuet = true
						self.isAllmuet = true;
						self.isMuteAllDialog = false;
					},
					err => {
						console.log(err);
					}
				);
			},

			unmuteAll() {
				//解除静音
				let self = this
				let mediaControlBuilder = new RL_MEET.MediaControlBuilder();
				mediaControlBuilder.setConfId(self.meetInfo.confId);
				mediaControlBuilder.setAction(3);
				mediaControlBuilder.setUnchangable(1);
				mediaControlBuilder.setAllMember(2);
				RL_MEET.MediaControl(
					mediaControlBuilder,
					e => {
                        console.log(e);
                        this.$message({
                            message: self.$t('last.unmuteTip')
                        });
                        self.$parent.isAllmuet = false;
                        self.isAllmuet = false;
                        // self.unmuteAllUnchangable()
					},
					err => {
						console.log(err);
					}
				);
			},
			unmuteAllUnchangable() {
				let self = this
				let mediaControlBuilder = new RL_MEET.MediaControlBuilder();
				mediaControlBuilder.setConfId(self.meetInfo.confId);
				mediaControlBuilder.setAction(3);
				mediaControlBuilder.setUnchangable(0);
				mediaControlBuilder.setAllMember(2);
				RL_MEET.MediaControl(
					mediaControlBuilder,
					e => {
						console.log(e);

						self.$parent.isAllmuet = false
					},
					err => {
						console.log(err);
					}
				);
			},
			updateListUserName(item) {
				var self = this;
				if (this.isHost || this.SelfMeetUserId == item.memberId) {
					self
						.$prompt("请输入想要更改名字", "更改名字", {
							confirmButtonText: "确定",
							cancelButtonText: "取消",
							inputValidator: self.checkName
						})
						.then(({
							value
						}) => {
							if (item.memberId == self.SelfMeetUserId) {
								// room.selfUser.updateUserName(value);
							} else {
								// var user = room.getUser(item.id);
								// user.updateUserName(value);
							}
						})
						.catch(function () {});
				}
			},
			//检查名字是否符合
			checkName(val) {
				var len = 0;
				var reg = /\s/g;
				if (reg.test(val)) {
					return "不允许输入空格";
				}
				for (var i = 0; i < val.length; i++) {
					if (val.charCodeAt(i) > 127 || val.charCodeAt(i) == 94) {
						len += 2;
					} else {
						len++;
					}
				}
				if (len == 0 || len > 16) {
					return "请输入1-16个字符长度";
				} else {
					return true;
				}
			},
		},
		filters: {
			FMTState(v) {
				if (v & 0x000200) {
					return "已拒绝";
				} else if (v & 0x000400) {
					return "接通中";
				} else if (v & 0x000800) {
					return "已挂断";
				} else if ((v & 0x000000) == 0) {
					return "已退出";
				} else {
					return v;
				}
			},

		},
	}
</script>

