<template>
  <div class="MeetingBg">
    <video v-show="false" autoplay muted ref="selfVideo"></video>
    <audio ref="audioTar" autoplay></audio>
    <meetingHeader v-show="!(IsShareIng && shareType==2)" :isLogin='isLogin' :netStatus="netStatus" :roomInfo="meetInfo"
                   :isHost="isHost" :showShareBtn="miniBoard" ref="meetHeader"></meetingHeader>
    <merberlist
      v-if="memberInObj[SelfMeetUserId]"
      :isshow="merberDialog"
      :SelfMeetUserId="SelfMeetUserId"
      :isHost="isHost"
      :screenUserId="screenUserId"
      :voiceIncentiveId="voiceIncentiveId"
    ></merberlist>
    <WhiteBoard
      ref="WhiteBoard"
      v-show="IsShareIng && shareType==2"
      :roomId="roomId"
      :userId="SelfMeetUserId"
      :isWhiteBoarOrganiser="isWhiteBoarOrganiser"
      :whiteBoardControllerDisabled="whiteBoardControllerDisabled"
      :wbInfo="wbInfo"
    ></WhiteBoard>
    <div class="MeetUserVideo">
      <div style="height:100%;">
        <!--单人在会议中-->
        <selfmeetVideo
          v-show="Object.getOwnPropertyNames(memberInObj).length==1"
          :userId="SelfMeetUserId"
        ></selfmeetVideo>
        <!--多人在会议中-->
        <!--一带一模式-->
        <div class="OneWithOneBg" v-show="!MoreWithMore&&Object.getOwnPropertyNames(memberInObj).length>1">
          <div v-show="IsShareIng && shareType == 1" class="oneMainScreenBg">
            <video autoplay muted ref="oneMainScreen" style="object-fit:contain;width:100%;height:100%;"></video>
            <p class="userNameP">{{screenShareIsMSG}}</p>
          </div>
          <meetVideo
            v-if="Object.getOwnPropertyNames(memberInObj).length&&PlayUserList[0]"
            v-show="!IsShareIng"
            :userId="PlayUserList[0]"
          ></meetVideo>
          <span class="isShowdargLiIcon" v-show="isshowdargLi">
                      <i
                        class="RSIcon RSIcon-guanbishexiangtou col2a5"
                        style="font-size:50px;"
                        @click="isshowdargLi=!isshowdargLi"
                      ></i>
                  </span>
          <div id="dargLi" v-show="!isshowdargLi">
            <selfmeetVideo
              :userId="SelfMeetUserId"
            ></selfmeetVideo>
          </div>
        </div>
        <!--一带三模式-->
        <div class="MoreWithMore" v-show="MoreWithMore&&Object.getOwnPropertyNames(memberInObj).length>1">
          <ul class="meet">
            <li class="Main" @mouseover="showLeftArrow = true" @mouseout="showLeftArrow = false">
              <div v-show="IsShareIng && shareType == 1" class="MainScreenBg">
                <video autoplay muted ref="moreMainScreen" style="object-fit:contain;"></video>
                <p class="userNameP">{{screenShareIsMSG}}</p>
              </div>
              <meetVideo
                v-if="PlayUserList[0]"
                v-show="PlayUserList[0]&&!IsShareIng"
                :userId="PlayUserList[0]"
              ></meetVideo>
              <img v-show="showLeftArrow &&
              (Object.getOwnPropertyNames(memberInObj).length > 4 || (IsShareIng && shareType == 1 && Object.getOwnPropertyNames(memberInObj).length > 3)) "
                   src="../../assets/img/meet/arrowLeft.png" @click="changeScreenMember('pre')"
                   v-preventReClick="2000"
                   class="arrow-left"
                   style="position: absolute;left: 0;top: 49%;margin-top: -40px;cursor:pointer;pointer-events:auto;"/>
            </li>
            <ul class="meet-right" @mouseover="showRightArrow = true" @mouseout="showRightArrow = false">
              <li>
                <div class="NoMerber" v-show="!PlayUserList[1]">{{ $t('meeting.waitingForEnter') }}</div>
                <meetVideo
                  v-show="PlayUserList[1]"
                  :userId="PlayUserList[1]"
                ></meetVideo>
              </li>
              <li>
                <div class="NoMerber" v-show="!PlayUserList[2]">{{$t('meeting.waitingForEnter')}}</div>
                <meetVideo
                  v-show="PlayUserList[2]"
                  :userId="PlayUserList[2]"
                ></meetVideo>
              </li>
              <li>
                <div class="NoMerber" v-show="!PlayUserList[3]">{{$t('meeting.waitingForEnter')}}</div>
                <meetVideo
                  v-show="PlayUserList[3]"
                  :userId="PlayUserList[3]"
                ></meetVideo>
              </li>
              <img v-show="(Object.getOwnPropertyNames(memberInObj).length > 4 || (IsShareIng && shareType == 1 && Object.getOwnPropertyNames(memberInObj).length > 3))  && showRightArrow"
                   @click="changeScreenMember('next')" src="../../assets/img/meet/arrowRight.png"
                   v-preventReClick="2000"
                   class="arrow-right"
                   style="position: absolute;right: 0;top: 49%;margin-top: -40px;cursor:pointer;pointer-events:auto;"/>
            </ul>
          </ul>
        </div>
      </div>
    </div>

    <footer v-if="memberInObj[SelfMeetUserId]" v-show="!(IsShareIng && shareType==2)">
      <ul>
        <el-tooltip effect="dark" :content="memberInObj[SelfMeetUserId].isOpenVideo?$t('meeting.closeCamera'):$t('meeting.openCamera')"
                    placement="top" v-if="needAV === 'all' || needAV === 'video' || SelfMeetUserId.includes('vistor')">
          <li
            style="pointer-events:auto;"
            :class="{isActive:memberInObj[SelfMeetUserId].isOpenVideo}"
            @click="memberInObj[SelfMeetUserId].isOpenVideo?closeCameraAndPubVideo():openCamera()"
            v-preventReClick
          >
            <i
              class="RSIcon"
              :class="memberInObj[SelfMeetUserId].isOpenVideo ? 'RSIcon-kaiqishexiangtou':'RSIcon-guanbishexiangtou'"
              style="font-size:48px;"
            ></i>
          </li>
        </el-tooltip>
        <el-tooltip
          effect="dark"
          v-if="memberInObj[SelfMeetUserId] && (needAV === 'all' || needAV === 'audio' || SelfMeetUserId.includes('vistor'))"
          :content="memberInObj[SelfMeetUserId].isOpenAudio?$t('meeting.closeMicrophone'):$t('meeting.openMicrophone')"
          placement="top">
          <li
            style="pointer-events:auto;"
            :class="{isActive:memberInObj[SelfMeetUserId].isOpenAudio}"
            @click="memberInObj[SelfMeetUserId].isOpenAudio?closeMicrophone():openMicrophone()"
            v-preventReClick
          >
            <i
              class="RSIcon"
              :class="memberInObj[SelfMeetUserId].isOpenAudio ? 'RSIcon-kaiqimaikefeng':'RSIcon-guanbimaikefeng'"
              style="font-size:30px;"
            ></i>
          </li>
        </el-tooltip>
        <!--<el-tooltip-->
        <!--effect="dark"-->
        <!--v-if="memberInObj[SelfMeetUserId]"-->
        <!--v-show="memberInObj[SelfMeetUserId].roleId==11"-->
        <!--:content="!(memberInObj[SelfMeetUserId].state&0x00020000)?'申请举手':'取消举手'"-->
        <!--placement="top">-->
        <!--<li-->
        <!--:class="{isActive:memberInObj[SelfMeetUserId].state&0x00020000}"-->
        <!--@click="!(memberInObj[SelfMeetUserId].state&0x00020000)?planHand():closePlanHand()"-->
        <!--&gt;-->
        <!--<i class="RSIcon RSIcon-jushou" style="font-size:30px;"></i>-->
        <!--</li>-->
        <!--</el-tooltip>-->
        <el-tooltip
          effect="dark"
          :content="videoStart?$t('meeting.stopRecord'):$t('meeting.startRecord')"
          placement="top"
          v-show="false">
          <li :class="{isActive:videoStart}" @click="videoStart?stopRecording():startRecording()">
            <i class="RSIcon RSIcon-luzhi" style="font-size:48px;"></i>
          </li>
        </el-tooltip>
        <el-tooltip effect="dark" :content="$t('meeting.layout')" placement="top">
          <li @click="isChangeLayout()">
            <i class="RSIcon RSIcon-buju" style="font-size:48px;"></i>
          </li>
        </el-tooltip>
        <li :class="{isActive:screenShareIsShow}" v-if="!screenShareIsShow">
          <el-dropdown :split-button="false" placement="top">
                        <span class="el-dropdown-link">
                            <i class="RSIcon RSIcon-gongxiang" style="font-size:48px;"></i>
                        </span>
            <el-dropdown-menu slot="dropdown" class="screenShare">
              <el-dropdown-item v-if="shareType != 2">
                <p @click="screenShare">{{$t('meeting.shareDesktop')}}</p>
              </el-dropdown-item>
              <!-- <el-dropdown-item>
                <p @click="shareWhiteBoard">{{$t('meeting.shareWhite')}}</p>
              </el-dropdown-item> -->
            </el-dropdown-menu>
          </el-dropdown>
        </li>
        <el-tooltip v-else effect="dark" :content="shareType===1?$t('meeting.closeScreenShar'):$t('meeting.closeWhiteSharing')" placement="top">
          <li
            @click="shareType===1?SelfCloseScreenShare():closeWhiteBoardShare()"
            :style="{background:'red'}"
          >
            <i class="RSIcon RSIcon-guanbigongxiang" style="font-size:48px;"></i>
          </li>
        </el-tooltip>
        <el-dropdown :split-button="false" placement="top-start">
          <span class="el-dropdown-link">
              <li>
                  <i class="RSIcon RSIcon-yaoqing" style="font-size:48px;"></i>
              </li>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-if="!(SelfMeetUserId.indexOf('vistor') > -1)">
              <p @click="inviteMerbers">邀请人员入会</p>
            </el-dropdown-item>
            <el-dropdown-item>
              <p @click="meetShareDialog = true">{{ $t('connect.metting') }}{{ $t('btn.share') }}</p>
            </el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-tooltip effect="dark" :content="$t('meeting.participant')" placement="top">
          <li @click="merberDialog=!merberDialog" :class="{isActive:merberDialog}">
            <i class="RSIcon RSIcon-canhuiren" style="font-size:48px;"></i>
          </li>
        </el-tooltip>
        <!-- <el-tooltip effect="dark" :content="$t('connect.news')" placement="top" v-if="meetInfo.chatGroupId">
          <li @click="chatDialog=!chatDialog,chatMSGValueNum=0;" :class="{isActive:chatDialog}">
            <el-badge :value="chatMSGValueNum" v-show="chatMSGValueNum">
              <i class="RSIcon RSIcon-qunliao"></i>
            </el-badge>
            <i class="RSIcon RSIcon-qunliao" v-show="!chatMSGValueNum" style="font-size:48px;"></i>
          </li>
        </el-tooltip> -->
        <el-dropdown :split-button="false" placement="top-start">
          <span class="el-dropdown-link">
              <li>
                  <i class="RSIcon RSIcon-gengduo1" style="font-size:48px;"></i>
              </li>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item v-if="isHost">
              <p @click="screenlockMeet">
                {{ $t('meeting.lockSharing') }}
                <span v-show="isScreenlockMeet">{{ $t('meeting.turnedOn') }}</span>
                <span v-show="!isScreenlockMeet">{{ $t('meeting.turnedOff') }}</span>
              </p>
              <!-- <el-switch class="fr" v-model="isScreenlockMeet"> </el-switch> -->
            </el-dropdown-item>
            <el-dropdown-item v-if="isHost">
              <p @click="lockMeet">
                {{ $t('meeting.lockMeeting') }}
                <span v-show="isLockMeet">{{ $t('meeting.turnedOn') }}</span>
                <span v-show="!isLockMeet">{{ $t('meeting.turnedOff') }}</span>
              </p>
            </el-dropdown-item>
            <!-- <el-dropdown-item>参会人布局</el-dropdown-item> -->
            <el-dropdown-item v-show="false">
              <p @click="SetLayoutDialog=!SetLayoutDialog">{{ $t('meeting.moderatorLayout') }}</p>
            </el-dropdown-item>
            <el-dropdown-item>
              <p @click="meetInfoDialog=true">{{ $t('meeting.meetingDetail') }}</p>
            </el-dropdown-item>
            <el-dropdown-item>
              <p @click="setvidedoialog()">{{ $t('meeting.meetingSetting') }}</p><!--会议设置-->
            </el-dropdown-item>
            <el-dropdown-item v-if="isHost && Object.getOwnPropertyNames(this.memberInObj).length > 2">
              <p @click="setMainHallDialog=true">{{ $t('meeting.setMainVenue') }}</p>
            </el-dropdown-item>
            <el-dropdown-item v-if="Object.getOwnPropertyNames(memberInObj).length > 4">
              <p @click="setRoundTourDialog=true">{{ $t('meeting.setRoundTour')}}</p>
            </el-dropdown-item>
            <el-dropdown-item v-if="false">
              <p @click="recordStart">录像</p>
            </el-dropdown-item>
            <!-- <el-dropdown-item>
                            <p @click="downlog()">日志下载</p>
                        </el-dropdown-item> -->
          </el-dropdown-menu>
        </el-dropdown>
      </ul>
    </footer>
    <div class="handUser" v-show="handUserMsg&&isHost&&isAllmuet">{{handUserMsg}}</div>
    <el-dialog
      :title="$t('modelMessage.prompt')"
      :visible.sync="confirmLeavep"
      width="450px"
      :modal="false"
      :close-on-click-modal="false"
      custom-class="confirmLeavepDialog"
      center
    >
      <div style="width:210px;margin:0 auto;line-height:25px;">
        <p style="font-size:18px; margin-bottom:20px;">{{ $t('meeting.meetingConfirm') }}</p>
        <el-checkbox v-if="isHost" v-model="isDelChecked">{{ $t('meeting.exitEndMeeting') }}</el-checkbox>
      </div>
      <span slot="footer" class="dialog-footer clearfix">
                <el-button type="default" @click="confirmLeavep=false">{{ $t('btn.cancel') }}</el-button>
                <el-button type="success" style="margin-left:50px;" @click="leaveAllMeet">{{ $t('btn.determine') }}</el-button>
            </span>
    </el-dialog>

    <el-dialog :title="$t('meeting.windowLayout')" :visible.sync="selectMain" :modal="false" width="500px" :close-on-click-modal="false">
      <el-row :gutter="20">
        <el-col :span="12">
          <ul
            class="selectOneVSOne"
            :class="{selecctMainActive:selecctMainIndex==0}"
            @click="selecctMainIndex=0"
          >
            <li>
              {{ $t('meeting.smartMode') }}
              <span></span>
            </li>
          </ul>
        </el-col>
        <el-col :span="12">
          <ul
            class="selectOneVMore"
            :class="{selecctMainActive:selecctMainIndex==1}"
            @click="selecctMainIndex=1"
          >
            <li>{{ $t('meeting.smartMode') }}</li>
            <li></li>
            <li></li>
            <li></li>
          </ul>
        </el-col>
      </el-row>
      <span slot="footer" class="dialog-footer">
                <el-button @click="selectMain = false;selecctMainIndex=MoreWithMore?0:1">{{ $t('btn.cancel') }}</el-button>
                <el-button type="primary" @click="changelaout">{{ $t('btn.determine') }}</el-button>
            </span>
    </el-dialog>
    <el-dialog
      :title="$t('connect.settings')"
      :visible.sync="setVideoDialog"
      width="500px"
      :modal="false"
      :close-on-click-modal="false"
      :modal-append-to-body="false"
      v-dialogDrag
      custom-class="setMedia"
    >
      <el-tabs v-model="activeName" tab-position="left">
        <el-tab-pane :label="$t('meeting.audio')" name="1">
          <p class="chooseEqu">{{ $t('meeting.microphone') }}</p>
          <el-select v-model="CurrentMicrophoneID" class="w100" :placeholder="$t('meeting.pleaseChoose')">
            <el-option
              v-for="item in MicrophoneList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <p class="chooseEqu">{{ $t('meeting.speaker') }}</p>
          <el-select
            v-model="CurrentSpeakerID"
            class="w100 audioChoose"
            :placeholder="$t('meeting.pleaseChoose')"
            @change="changeAudioDestination()"
          >
            <el-option
              v-for="item in SpeakerList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
        </el-tab-pane>
        <!-- <el-tab-pane label="扬声器" name="2"> -->

        <!-- </el-tab-pane> -->
        <el-tab-pane :label="$t('message.video')" name="2">
          <p class="chooseEqu">{{ $t('meeting.camera') }}</p>
          <el-select
            v-model="CurrentCameraID"
            class="w100"
            :placeholder="$t('meeting.pleaseChoose')"
            @change="PreviewingVideo()"
          >
            <el-option
              v-for="item in CameraList"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            ></el-option>
          </el-select>
          <video class="selfvide" ref="SelectVideo2" muted autoplay></video>
        </el-tab-pane>
        <div class="save-media" @click="saveSet">{{ $t('btn.save')}}</div>
      </el-tabs>
      <!--<div class="tc">-->
      <!--<el-button type="primary" round class="w300" style="margin:10px auto;" @click="saveSet">保存</el-button>-->
      <!--</div>-->
    </el-dialog>
    <!-- 设置主会场 -->
    <setMainHall v-if="memberInObj[SelfMeetUserId]" :isShow="setMainHallDialog"
                 :SelfMeetUserId="SelfMeetUserId"></setMainHall>
    <!-- 会议分享 -->
    <meetShare :isshow="meetShareDialog" :meetInfos="meetInfo"></meetShare>
    <!-- 会议延时 -->
    <delayed :isshow="prolongDialogVisible" :willNextEndTime="nextEndTime"></delayed>
    <!-- 会议信息 -->
    <meetInfo :isShow="meetInfoDialog" :roomInfo="meetInfo" :isHost="isHost"></meetInfo>
    <!-- 设置轮巡 -->
    <setRoundTour v-if="memberInObj[SelfMeetUserId]" :isShow="setRoundTourDialog"
                 :SelfMeetUserId="SelfMeetUserId"></setRoundTour>
    <!-- 邀请 -->
    <!-- <Invite
      v-if="SelfMeetUserId&&!(SelfMeetUserId.indexOf('vistor') > -1)"
      :IsShow="addMerber"
      @ListenCallback="inviteMerber"
      :SelectedArr="SelectedArr"
      :roomId="roomId"
      :title="$t('meeting.inviteParticipant')"
      :oldtitle="$t('meeting.SelectParticipant')"
    ></Invite> -->
    <!-- <chat :isShow="!isNewMeetingChat && chatDialog" :MSGList="MSGList"></chat> -->
    <!-- <chat-new :isShow="isNewMeetingChat && chatDialog"></chat-new> -->
    <setLayout :isShow="SetLayoutDialog" :merberLists="MerberiList2" :isHost="isHost"></setLayout>
  </div>
</template>

<script>
  import Meet from "../../../public/SDK/mediaMeeting";

  import meetingHeader from "../meeting/meetingHeader"; //会中头部信息
  import meetInfo from "../meeting/meetInfo"; //会议信息弹框
  import meetShare from "../meeting/meetShare"; //延时弹框
  import delayed from "../meeting/delayed"; //延时弹框
  // import Invite from "../Invite"; //邀请参会人弹框
  import merberlist from "../meeting/merberlist"; //参会人列表
  import setMainHall from "../meeting/setMainHall"; //设置主会场

  // import chat from "../meeting/chat"; //会中im
  // import chatNew from "../meeting/chat_new"; //会中im
  import setLayout from "../meeting/setLayout"; //指定参会人

  import speakstaus from "../meeting/speakstaus"; //参会人图标状态

  import defaultImg from "../../assets/img/auth/defaultAvatar.png"; //默认头像
  import meetVideo from "../meeting/meetVideo";
  import selfmeetVideo from "../meeting/selfmeetVideo";
  import RecordRTC from "recordrtc";
  // import rongshi from '../../../public/rsconfig'
  // RSIsShowDownLog

  //白板
  import WhiteBoard from "../whiteBoard/index";
  import {mapState, mapGetters, mapMutations} from "vuex";

  import {NoteTypes, NoteTypesAction} from "../../plugins/NoteType";

  import {Debugout} from 'Debugout'

  import setRoundTour from "../meeting/setRoundTour"; //设置轮巡

  export default {
    computed: {
      ...mapState(["LoginServe"]),
      ...mapGetters(['memberInObj','user_account', 'meetInfo', 'memberOutObj', 'meetSetting','user_info','allMessages']),
      confId() {
        return this.meetConfId || this.queryConfId
      }
    },
    components: {
      speakstaus,
      meetingHeader,
      meetInfo,
      delayed,
      meetShare,
      // Invite,
      merberlist,
      setMainHall,
      // chat,
      // chatNew,
      setLayout,
      meetVideo,
      selfmeetVideo,
      WhiteBoard,
      setRoundTour
    },
    data() {
      return {
        mainVenue: '', //主会场成员id
        showLastVideo: false, //判断屏幕共享切屏时，是否显示后面的成员
        shareType: 1, //共享方式  1:分享桌面 2:白板
        whiteBoardRoomId: "", //白板房间id
        whiteBoardUserId: '',
        isWhiteBoarOrganiser: false, //白板发起人默认为true
        whiteBoardControllerDisabled: false, //白板控制条是否能用
        totalPage: 10, // 白板页数
        current: 1, //画板页码
        wbInfo: {},
        handUserMsg: "",
        isshowdargLi: false,
        loading: "",
        roomId: "",
        SelfMeetUserId: "", //userid+@
        userName: "",
        // meetInfo: {},
        joinState: 0,
        isLockMeet: false,
        MerberiList: {},
        MerberiList2: [],
        PlayUserList: [],
        photoList: {},
        defaultImg: defaultImg,
        netStatus: {},
        MSGList: [], //'聊天数组'
        isHost: false,
        isShowCamera: false,
        isShowAudio: false,
        isFullScreenStatus: false,
        screenShareIsShow: false,
        chatMSGValueNum: 0,
        video: null,
        videoStart: false,
        recorder: null,

        MoreWithMore: true,

        merberDialog: false,
        confirmLeavep: false,
        chatDialog: false, //聊天
        isDelChecked: true,
        SetLayoutDialog: false,
        meetInfoDialog: false,
        meetShareDialog: false,
        prolongDialogVisible: false,
        setVideoDialog: false,
        setMainHallDialog: false,
        activeName: "1",
        CurrentMicrophoneID: "",
        MicrophoneList: [],
        CurrentCameraID: "",
        CurrentSpeakerID: "",
        CameraList: [],
        SpeakerList: [],
        nextEndTime: "",
        oldId: {},

        addMerber: false,
        SelectedArr: [],
        SelectedList: [],

        screenShareIsMSG: "",
        IsShareIng: false,
        IsMainHall: false,
        selectMain: false,
        selecctMainIndex: 0,
        screenUserId: "",
        isScreenlockMeet: false,
        RSIsShowDownLog: false,
        isAllmuet: false,
        SaveStreamList: {},

        selfVideoModalShow: false,
        selfCodec: "",  // 自己的编码类型;
        reqVideoLists: [],  // 已经请求过成员视频列表;
        antiSsrc: "", // 请求视频的ssrc;
        screenStream: {},
        RSServe: "",
        showRightArrow: false,
        showRightFlag: true,
        showLeftArrow: false,
        showLeftFlag: true,
        pageNo: 1,
        memberInArrary: [],
        historyArr: [],
        isMediaConnect: false,
        queryConfId: "",
        isLogin: true,
        needAV: 'all',
        miniBoard: false,
        mediaType: 1,
        setRoundTourDialog: false,
        roundTourTimer:null,
        recordObj:{
          palyState:0,
          pauseState:0,
          cancleState:0
        },
        voiceIncentiveId:"",
      };
    },
    props: {
      meetConfId: String,
      isShow: Boolean,
      joinMeetingState:Object
    },
    watch: {
      isShow(newVal) {
        console.log('===newVal===', newVal);
        if (this.shareType === 2 && this.IsShareIng) {
          if (newVal) {
            this.leaveWbRoom(true, true)
          } else {
            this.joinWhiteBoardRoom(Number(this.whiteBoardRoomId), this.whiteBoardUserId)
          }
        }
      },
      PlayUserList: {
        deep: true,
        handler(newVal, oldVal) {
          console.log('PlayUserList change', newVal, oldVal);
          oldVal.forEach(item => {
