      screenlockMeet() {
        let that = this;
        if (!this.isHost) {
          that.$message({
            message: this.$t('meeting.haveNoPermission'),
            type: "error"
          });
          return;
        }
        let acon = 0;
        let msg = this.$t('meeting.theSharingIsLocked')
        if (this.isScreenlockMeet) {
          acon = 3;
          msg = this.$t('meeting.theSharingIsUnlocked')
        } else {
          acon = 2;
        }
        let LockMeetBuilder = new RL_MEET.LockMeetBuilder();
        LockMeetBuilder.setAction(acon);
        LockMeetBuilder.setConfId(that.roomId);
        RL_MEET.LockMeet(
          LockMeetBuilder,
          res => {
            this.isScreenlockMeet = !this.isScreenlockMeet;
            this.$message.info(msg)
          },
          function (error) {
            console.info(error);
          }
        );
      },
      lockMeet() {
        let that = this;
        if (!this.isHost) {
          that.$message({
            message: this.$t('meeting.haveNoPermission'),
            type: "error"
          });
          return;
        }
        let acon = 0;
        let msg = this.$t('meeting.theMeetingIsLocked')
        if (this.isLockMeet) {
          acon = 1;
          msg = this.$t('meeting.theMeetingIsUnlocked')
        } else {
          acon = 0;
        }
        let LockMeetBuilder = new RL_MEET.LockMeetBuilder();
        LockMeetBuilder.setAction(acon);
        LockMeetBuilder.setConfId(that.roomId);
