      handUser() {
        let self = this;
        let handCount = 0;
        let handNames = [];
        for (var user in self.MerberiList) {
          if (
            self.MerberiList[user].state & 0x00020000 &&
            self.MerberiList[user].roleId == 11
          ) {
            handCount++;
            if (handCount < 3) {
              handNames.push(self.MerberiList[user].userName);
            }
          }
        }
        var handMsg = "";
        if (handCount == 1) {
          handMsg = handNames[0] + self.$t('meeting.raiseHandForSpeak');
        }

        if (handCount > 1) {
          handMsg =
            handNames[0] + "、" + handNames[1] + self.$t('meeting.wait') + handCount + this.$t('meeting.raiseHand');
        }
        if (handCount > 0) {
          self.handUserMsg = handMsg;
        } else {
          self.handUserMsg = "";
        }
      },
