      inviteMerbers() {
        let self = this;
        self.SelectedList = [];
        let memberInObj = JSON.parse(JSON.stringify(this.memberInObj));
        let memberOut = JSON.parse(JSON.stringify(this.memberOutObj));
        let members = Object.assign(memberInObj, memberOut);
        console.log('members---', members)
        for(let key in members){
          const username = members[key].username
          if (key && key.indexOf("@") > -1) {
            key = key.substr(0, key.indexOf("@"));
          }
          self.SelectedList.push({account:key, username});
        }
        const memberCount = this.meetInfo.maxMember - Object.keys(this.memberInObj).length - Object.keys(this.memberOutObj).length
        self.$bus.emit('addPerson',true, memberCount, self.SelectedList, self.inviteMerber, true)
