      // 获取媒体设备信息;
      getMediaDeviceList() {
        let self = this;
        var currentSpeakerID;
        var currentCameraID;
        var currentMicrophoneID;
        var speakers = [];
        var cameras = [];
        var microphone = [];

        RL_Media.GetMediaDevice(deviceList => {
          console.log(deviceList, "deviceList===========")
          for (var key in deviceList) {
            var val = deviceList[key];
            if (val.kind == 'audiooutput') {
              speakers.push({
                "name": val.label,
                "id": val.deviceId
              });
              if (val.deviceId == 'default') {
                currentSpeakerID = self.CurrentSpeakerID ? self.CurrentSpeakerID : val.deviceId;
              } else {
                currentSpeakerID = val.deviceId;
              }
            }
            if (val.kind == 'videoinput') {
              cameras.push({
                "name": val.label,
                "id": val.deviceId
              });
              if (!currentCameraID) {
                if (val.deviceId == 'default') {
                  currentCameraID = self.CurrentCameraID ? self.CurrentCameraID : val.deviceId;
                } else {
                  currentCameraID = val.deviceId;
                }
              }
            }
            if (val.kind == 'audioinput') {
              microphone.push({
                "name": val.label,
                "id": val.deviceId
              });
              if (val.deviceId == 'default') {
                currentMicrophoneID = self.CurrentMicrophoneID ? self.CurrentMicrophoneID : val.deviceId;
              } else {
                currentMicrophoneID = val.deviceId;
              }
            }
          }
          // 扬声器列表;
          this.SpeakerList = speakers;
          // 摄像头列表;
          this.CameraList = cameras;
          // 麦克风列表;
          this.MicrophoneList = microphone;
          this.CurrentSpeakerID = currentSpeakerID;
          this.CurrentCameraID = currentCameraID;
          this.CurrentMicrophoneID = currentMicrophoneID;
          this.PreviewingVideo();
        });
      },
      // 会中设备更换重新绑定;
      attachSinkId(element, sinkId) {
        element
          .setSinkId(sinkId)
          .then(() => {
            console.log(`Success, audio output device attached: ${sinkId}`);
          })
          .catch(error => {
            let errorMessage = error;
            if (error.name === "SecurityError") {
              errorMessage = `You need to use HTTPS for selecting audio output device: ${error}`;
            }
            console.error(errorMessage);
            // Jump back to first output device in the list as it's the default.
          });
      },
