<template>
  <div>
    <el-dialog title="参会人布局" width="600px" :visible.sync="isShow" center :before-close="handleClose">
      <!-- 内层嵌套的弹窗 -->
      <el-dialog width="400px" title="配置" :visible.sync="innerVisible" center append-to-body>
        <div style="margin-bottom: 10px;" v-if="false">
          <span>布局选项：</span>
          <el-radio-group v-if="assignIndex == '0'" v-model="checkIsAll" @change="choosePerson">
            <el-radio :label="1">轮巡所有人</el-radio>
            <el-radio :label="2">轮巡指定人</el-radio>
          </el-radio-group>
          <el-radio-group v-else-if="assignIndex == '1'" v-model="checkIsAll1" @change="choosePerson">
            <el-radio :label="1">轮巡所有人</el-radio>
            <el-radio :label="2">轮巡指定人</el-radio>
          </el-radio-group>
          <el-radio-group v-else-if="assignIndex == '3'" v-model="checkIsAll3" @change="choosePerson">
            <el-radio :label="1">轮巡所有人</el-radio>
            <el-radio :label="2">轮巡指定人</el-radio>
          </el-radio-group>
          <el-radio-group v-else-if="assignIndex == '4'" v-model="checkIsAll4" @change="choosePerson">
            <el-radio :label="1">轮巡所有人</el-radio>
            <el-radio :label="2">轮巡指定人</el-radio>
          </el-radio-group>
        </div>
        <div style="margin-bottom: 10px;">
          <p style="margin-bottom: 10px;">轮巡时间：</p>
          <el-radio-group v-model="radioTime">
            <el-radio :label="10">10秒</el-radio>
            <el-radio :label="30">30秒</el-radio>
            <el-radio :label="60">60秒</el-radio>
            <el-radio :label="120">120秒</el-radio>
          </el-radio-group>
        </div>
        <div style="margin-bottom: 10px;">
          <p style="margin-bottom: 10px;">选择人员：</p>
          <el-checkbox-group v-model="checkList" @change="assignMembers">
            <div v-for="item in merberLists" :key="item.key">
              <el-checkbox :label="item.memberId" v-model="item.isChecked" :name="item.userName">
                <img width="20px" height="20px" :src="defaultAvatar" alt="">
                <span>{{item.isChecked}} {{ item.userName }}</span>
              </el-checkbox>
            </div>
          </el-checkbox-group>
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="cancelAssign">取 消</el-button>
          <el-button type="primary" @click="confirmAssignMember()">保存</el-button>
        </span>

      </el-dialog>

      <el-col :span="4">
        <ul class="oneToOne" @click="radioLayout='1_1'">
          <li></li>
        </ul>
        <el-radio class="oneToOneRadio" v-model="radioLayout" label="1_1"> * </el-radio>
        <ul class="oneToThree" @click="radioLayout='1_3'">
          <li></li>
          <li></li>
          <li></li>
          <li></li>
        </ul>
        <el-radio class="oneToOneRadio" v-model="radioLayout" label="1_3"> * </el-radio>
      </el-col>
      <!-- 一带一模式选择轮巡 -->
      <el-col class="oneToOneLayout" v-if="radioLayout=='1_1'" :span="20" style="height: 300px">
        <div class="dialogMainDiv">
          <p @click="assignMember(),assignIndex = '0'" v-if="checkIsAll == 1">轮巡所有人</p>
          <p @click="assignMember(),assignIndex = '0'" v-else-if="checkIsAll == 2">轮巡指定人</p>
          <p @click="assignMember(),assignIndex = '0'" v-else-if="checkIsAll == 0">语音激励</p>
          <el-col style="height: 100px">
            <div class="oneToOneMyself">
              <p>自己不可指定</p>
            </div>
          </el-col>
        </div>
      </el-col>
      <!-- 一带三模式选择轮巡 -->
      <div class="oneToThreeLayout" v-if="radioLayout=='1_3'">
        <el-col :span="16" style="height: 300px">
          <div class="dialogMainDiv">
            <p @click="assignMember(),assignIndex = '1'" v-if="checkIsAll1 == 1">轮巡所有人</p>
            <p @click="assignMember(),assignIndex = '1'" v-else-if="checkIsAll1 == 2">轮巡指定人</p>
            <p @click="assignMember(),assignIndex = '1'" v-else-if="checkIsAll1 == 0">语音激励</p>
          </div>
        </el-col>
        <el-col :span="4" style="height: 300px">
          <div class="dialogMyself">
            <p>自己不可指定</p>
          </div>
          <div class="dialogRightDiv">
            <p @click="assignMember(),assignIndex = '3'" v-if="checkIsAll3 == 1">轮巡所有人</p>
            <p @click="assignMember(),assignIndex = '3'" v-else-if="checkIsAll3 == 2">轮巡指定人</p>
            <p @click="assignMember(),assignIndex = '3'" v-else-if="checkIsAll3 == 0">语音激励</p>
          </div>
          <div class="dialogRightDiv">
            <p @click="assignMember(),assignIndex = '4'" v-if="checkIsAll4 == 1">轮巡所有人</p>
            <p @click="assignMember(),assignIndex = '4'" v-else-if="checkIsAll4 == 2">轮巡指定人</p>
            <p @click="assignMember(),assignIndex = '4'" v-else-if="checkIsAll4 == 0">语音激励</p>
          </div>
        </el-col>
      </div>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeLayout">关闭布局</el-button>
        <el-button @click="assignHandleClose">取 消</el-button>
        <el-button type="primary" @click="confirmAssign">启用布局</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
  import defaultAvatar from "../../assets/img/auth/defaultAvatar.png";

  export default {
    props: ['isShow', 'merberLists', 'isHost', 'confId'],
    watch: {
      checkList(val) {
        for (var j = 0; j < this.merberLists.length; j++) {
          if (this.checkList.indexOf(this.merberLists[j].memberId) > -1) {
            this.$set(this.merberLists[j], 'isChecked', true)
          } else {
            this.$set(this.merberLists[j], 'isChecked', false)
          }
        }
      },  memberList(val) {
        this.merberLists = []
        if(val){
          for (let i = 0; i < val.length; i++) {
            let merber = val[i];
              if ((merber.state & 0x000001) && (merber.state & 0x000100)) {
                this.merberLists.push(merber)
              }
          }
        }

      }
    },
    data() {
      return {
        checkList: [],
        assignMemberDialog: false,
        innerVisible: false,
        assignArray: [],
        assignArray1: [],
        assignArray3: [],
        assignArray4: [],
        newAssignArray: '',
        newAssignArray1: '',
        newAssignArray3: '',
        newAssignArray4: '',
        assignIndex: 1,
        radioTime: 10,
        checkIsAll: 0,
        checkIsAll1: 0,
        checkIsAll3: 0,
        checkIsAll4: 0,
        defaultAvatar: defaultAvatar,
        radioLayout: '1_1',
      }
    },
    created() {
      console.log(this.memberList, "this.memberList")
    },
    methods: {
      choosePerson() {
        if (this.checkIsAll == 1 || this.checkIsAll1 == 1 || this.checkIsAll3 == 1 || this.checkIsAll4 == 1) {
          console.log("我要选择全部人啦...")
          // todo;
        }

      },
      assignWindow() {
        this.assignMemberDialog = true;
      },
      assignMember() {
        this.innerVisible = true;

      },
      assignHandleClose() {
        this.checkList = [];
        this.$parent.closeDialog('isSetLayout');
      },
      arrayNorepeat(arr) {
        var newArr = [];
        for (var i = 0; i < arr.length; i++) {
          if (newArr.indexOf(arr[i]) == -1) {
            newArr.push(arr[i]);
          }
        }
        return newArr;
      },
      assignMembers() {
        console.log("开始选人了", this.checkList);
      },
      handleClose() {
        this.$parent.closeDialog('isSetLayout');
      },
      cancelAssign() {
        for (var j = 0; j < this.merberLists.length; j++) {
          if (this.checkList.indexOf(this.merberLists[j].memberId) > -1) {
            this.$set(this.merberLists[j], 'isChecked', false)
          }
        }
        this.innerVisible = false;
        this.checkList = [];
      },
      setLayoutObj(position, interval, assignArrayVal) {
        var screenArr = [];
        for (var i = 0; i < this.checkList.length; i++) {
          var screenObj = {};
          for (var j = 0; j < this.merberLists.length; j++) {
            var positionObj = {};
            if (this.checkList[i] == this.merberLists[j].memberId) {
              positionObj.pos = position,
                positionObj.interval = interval,
                screenObj.userName = this.merberLists[j].userName;
              screenObj.memberID = this.checkList[i],
                screenObj.idType = this.merberLists[j].idType,
                screenArr.push(screenObj);
              positionObj.screen = screenArr,
                assignArrayVal.push(positionObj);
            }
          }
        }
      },
      confirmAssignMember() {
        // 1v1主屏;
        if (this.assignIndex == 0) {
          this.setLayoutObj(0, this.radioTime * 1000, this.assignArray);
          console.log(this.assignArray, this.radioTime * 1000, "这是1_1第一个窗口的array")
          this.newAssignArray = this.setLayoutPosition(this.assignArray);

        }
        if (this.assignIndex == 1) {
          this.setLayoutObj(0, this.radioTime * 1000, this.assignArray1);
          console.log(this.assignArray1, this.radioTime * 1000, "这是1_3第一个窗口的array")
          this.newAssignArray1 = this.setLayoutPosition(this.assignArray1);

        }
        if (this.assignIndex == 3) {
          this.setLayoutObj(2, this.radioTime * 1000, this.assignArray3);
          console.log(this.assignArray3, this.radioTime * 1000, "这是1_3第三个窗口的array")
          this.newAssignArray3 = this.setLayoutPosition(this.assignArray3);
        }
        if (this.assignIndex == 4) {
          this.setLayoutObj(3, this.radioTime * 1000, this.assignArray4);
          console.log(this.assignArray4, this.radioTime * 1000, "这是1_3第四个窗口的array");
          this.newAssignArray4 = this.setLayoutPosition(this.assignArray4);
        }
        this.innerVisible = false;
        this.checkList = [];
      },
      setLayoutPosition(obj) {
        if (obj) {
          var position = '';
          for (var i = 0; i < obj.length; i++) {
            position ={
              "Pos": [obj[i].pos],
              "screen": obj[i].screen,
              "interval": obj[i].interval
            }
          }
          console.log(position, "这是return出去的拼好的position");
          return position;
        }
      },
      setLayoutReq(assignArray) {
        let that=this
        let SetLayoutBuilder = new RL_MEET.SetLayoutBuilder();
        SetLayoutBuilder.setConfId(this.confId);
        SetLayoutBuilder.setLayoutIdx(this.radioLayout);
        SetLayoutBuilder.setLayoutPos(this.$Base64.encode(JSON.stringify(assignArray)));
        // SetLayoutBuilder.setMainVenue();   // 设置主会场模式 暂时用不到;
        SetLayoutBuilder.setDeviceType("21");
        RL_MEET.SetLayout(SetLayoutBuilder, e => {
          var resp = JSON.parse(e.result);
          if (resp.statusCode == "000000") {
            that.$message({
              message: "设置成功",
              type: "success"
            });
            that.$parent.closeDialog('isSetLayout');
            this.checkList=[]
          }
        }).catch(() => {
          that.$message({
            type: 'info',
            message: '已取消设置'
          });
        });
      },
      confirmAssign() {
        let that = this;
        this.$confirm('您确定执行本次操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
            var assignArray = [];
            if (this.newAssignArray) {
              assignArray.push(this.newAssignArray)
            }
            if (this.newAssignArray1) {
              assignArray.push(this.newAssignArray1)
            }
            if (this.newAssignArray3) {
              assignArray.push(this.newAssignArray3)
            }
            if (this.newAssignArray4) {
              assignArray.push(this.newAssignArray4);
            }
            console.log(assignArray, this.newAssignArray1, "这是指定的人呢");
            that.setLayoutReq(assignArray);
          })
      

      },
      closeLayout() {
        let that = this;
        this.$confirm('您确定执行本次操作吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.setLayoutReq();
          err => {
            console.log(err);
          }
        });
        // that.$message({
        //   type: 'success',
        //   message: '取消布局成功!'
        // });
      },
    }
  }
</script>

<style>
  .oneToOneLayout .dialogMainDiv {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .oneToOneLayout .oneToOneMyself {
    position: absolute;
    right: 10px;
    bottom: 10px;
    border: 1px solid #222;
    width: 200px;
    height: 120px;
    cursor: auto;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .oneToOneLayout .dialogMainDiv p {
    height: 30px;
    width: 100px;
    position: absolute;
    left: 30%;
    top: 40%;
  }

  .oneToOne {
    width: 80px;
    height: 40px;
    border: 1px solid #222;
    position: relative;
    cursor: pointer;
  }

  .oneToOneRadio {
    margin: 10px 0 30px 30px;
    color: #fff;
  }

  .oneToOneRadio .el-radio__input.is-checked+.el-radio__label {
    color: #fff;
  }

  .oneToOne li {
    width: 20px;
    height: 10px;
    border: 1px solid #222;
    position: absolute;
    right: 0;
    bottom: 0;

  }
  .oneToThree  {
    cursor: pointer;
  }
  .oneToThree li:nth-child(1) {
    width: 60px;
    height: 43px;
    border: 1px solid #222;
    float: left;
  }

  .oneToThree li {
    width: 20px;
    height: 13px;
    float: left;
    border: 1px solid #222;

  }

  .dialogMainDiv {
    height: 274px;
    border: 1px solid #222;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
  }

  .dialogMyself,
  .dialogRightDiv {
    height: 90px;
    border: 1px solid #222;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  .dialogRightDiv {
    cursor: pointer;
  }
</style>


